<template>
  <div class="cellSetting">
    <el-dialog
      v-drag-dialog
      :title="title"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :modal-append-to-body="false"
      :width="width+'px'"
      :height="height+'px'"
      center
    >
      <el-table ref="multipleTable" border :data="cellObj" tooltip-effect="dark" :height="height-100" @selection-change="handleSelectionChange" :row-class-name="roWIndex">
        <el-table-column type="selection"  width="55" align="center" label-class-name="blue" :selectable="selectable"/>
        <!-- </el-table-column> -->
        <el-table-column label="列名" align="center">
          <template slot-scope="scope">{{ scope.row.displayName }}</template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button @click="handleAction" :disabled="newCell.length === 0" type="primary" >确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {get,post,postJson} from '@/config/axios.config'
import { debug } from 'util';
export default {
  props: {
    menuName:{
      type: [Number, String],
      default: ""
    },
    title: {
      type: [Number, String],
      default: "显示列设置"
    },
    width: {
      type: [Number],
      default: 300
    },
    height: {
      type: [Number],
      default: 500
    },
    cellObj: {
      type: [Object, Array],
      default: () => []
    },
    spreadHeadData: {
      type: [Object, Array],
      default: () =>[]
    }
  },
  data() {
    return {
      dialogVisible:false,
      newCell:[]
    };
  },
  methods: {
    selectable(row,index){
      if(row.hlock){
        return false;
      }else {
        return true;
      }
    },
    msg(val, type = 'warning') {
      this.$message({
        showClose: true,
        message: val,
        type: type,
        duration: 3000,
      });
    },
    sortBy(filed,rev,primer){
      rev = (rev) ? -1 : 1;
      return function (a, b) {
        a = a[filed];
        b = b[filed];
        if (typeof (primer) != 'undefined') {
          a = primer(a);
          b = primer(b);
        }
        if (a < b) { return rev * -1; }
        if (a > b) { return rev * 1; }
        return 1;
      }
    },
    open(){
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs.multipleTable.clearSelection();
        this.spreadHeadData.forEach(row => {
          this.cellObj.some((rows,index) => {
            if(rows.hlock){
              this.$refs.multipleTable.toggleRowSelection(this.cellObj[index],true);
            }else{
              if(row.displayName === rows.displayName){
                this.$refs.multipleTable.toggleRowSelection(this.cellObj[index]);
                return true;
              }
            }
          });
        });
      });
    },
    roWIndex({ row, rowIndex }) {
      row.hindex = rowIndex;
    },
    handleSelectionChange(val){
      this.newCell = val.sort(this.sortBy("hindex",false,parseInt));
    },
    handleAction(){
      let option={
        titles:JSON.stringify(this.newCell),
        menu:this.menuName
      }
      post("/basic/title/saveOrUpdate",option,false,false).then(res => {
        if(res.code === 0){
          this.$emit('callBack', this.newCell);
          this.handleClose();
        }else{
          this.msg(res.message);
        }
      })
      .catch(e => {
        this.msg(e.message);
      });
    },
    handleClose(){
      this.dialogVisible = false;
      this.newCell = [];
    }
  }
};
</script>
<style lang="scss">
.cellSetting{
.el-table--small td,
  .el-table--small th {
    padding: 0px
  }
