<template>
  <div class="bill-box" :style="{'height': height + 'px'}">
    <div class="bill-code" v-if="onlyBill">
      <span v-for="item in singleText">{{item}}</span>
    </div>
    <div class="two-bill-code" v-if="!onlyBill">
      <span :class="[type ? 'select-bg' : 'un-select-bg']" @click="billType">{{twoText[0]}}</span>
      <span :class="[type ? 'un-select-bg' : 'select-bg']" @click="billType">{{twoText[1]}}</span>
    </div>
    <el-input
      type="textarea"
      class="bill-textarea"
      :rows="6"
      resize="none"
      maxlength="20999"
      :style="{width: width + 'px'}"
      :placeholder="placeholderText"
      v-bind:value="value"
      v-on:input="$emit('input', billCode)"
      v-model="billCode">
    </el-input>
    <div class="bill-clear" @click="clearCode">
      <span>清</span>
      <span>空</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "sz-bill-code",
    data(){
      return {
        billCode: '',
        type: true
      }
    },
    computed: {
      placeholderText(){
        let text = '可输入多个' + this.singleText + '，以回车键隔开';
        if(!this.onlyBill){
          if(this.type && this.twoText.length === 2){
            text = '可输入多个' + this.twoText[0] + '，以回车键隔开';
          }else{
            text = '可输入多个' + this.twoText[1] + '，以回车键隔开';
          }
        }
        return text
      }
    },
    methods: {
      clearCode(){
        this.billCode = '';
        this.$emit('input', '');
      },
      billType(){
        this.type = !this.type;
        this.$emit('typeChange', this.type)
      }
    },
    props: {
      value: [String, Number],
      onlyBill: {
        type: Boolean,
        default: true
      },
      singleText: {
        type: String,
        default: '单号'
      },
      height: {
        type: Number,
        default: 114
      },
      width: {
        type: Number,
        default: 130
      },
      twoText: {
        type: Array,
        default: function(){
          return ['单号','包号']
        },
        validator(arr){
          return arr.length === 2
        }
      },
      resetType: {
        type: Number,
        default: 0
      }
    },
    watch: {
      value(newVal, oldVal){
        this.billCode = newVal;
        this.$emit("input", newVal);
      },
      resetType(newVal, oldVal){
        if(newVal > 0){
          this.type = true;
          this.$emit('typeChange', this.type)
        }
      }
    }
  }
</script>

<style scoped>
  .bill-box{
    display: inline-flex;
  }

  .bill-textarea{
    width: 130px;
  }

  .bill-code{
    width: 18px;
    color: #fff;
    background: #33acf9;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 5px 0;
  }

  /*多个单号的情况*/
  .two-bill-code{
    width: 18px;
    color: #fff;
    font-size: 12px;
    text-align: center;
  }

  .select-bg{
    background: #33acf9;
  }
  .un-select-bg{
    background: #d6d6d6;
  }

  .two-bill-code > span{
    display: flex;
    height: 50%;
    cursor: pointer;
    transition: all .2s;
    align-items: center;
  }

  .bill-clear{
    width: 18px;
    color: #fff;
    font-size: 12px;
    background: #ff6632;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 5px 0;
    cursor: pointer;
  }
</style>
