<template>
  <el-dialog
    class="dialog-changesite"
    title="切换网点"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :modal-append-to-body="false"
    :before-close="clear"
    :visible.sync="dialogVisible"
    width="40%"
  >
    <div class="top-search">
      <el-input
        v-model="value"
        placeholder="请输入内容"
        @keydown.enter.native="query"
      ></el-input>
      <el-button
        class="search-button"
        type="primary"
        icon="el-icon-search"
        @click="query"
        >搜索</el-button
      >
    </div>
    <div>
      <div class="table-head">
        <table>
          <tr>
            <td style="width:30%;">网点编码</td>
            <td style="width:40%;">网点名称</td>
            <td>&nbsp;&nbsp;&nbsp;&nbsp;操作</td>
          </tr>
        </table>
      </div>
      <div class="table-body">
        <table>
          <tr v-for="site in options" :key="site.index">
            <td style="width:30%;" v-text="site.code">网点编码</td>
            <td style="width:40%;" v-text="site.name">网点名称</td>
            <td @click="changeSite(site)">
              <el-button
                class="change-site"
                type="primary"
                icon="el-icon-success"
                >操作</el-button
              >
            </td>
          </tr>
        </table>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import {getSiteList} from '../../config/api';
export default {
  props: {
    visible: Boolean,
  },
  data() {
    return {
      value: '',
      chooseObj: null,
      loading: false,
      timer: null,
      options: [],
      dialogVisible: false,
      isFirstEnter: true,
    };
  },
  computed: {},
  methods: {
    clear() {
      this.dialogVisible = false;
      this.$emit('clear', false);
    },
    getSite(key) {
      let _this = this;
      let data = {name: key};
      getSiteList(data).then(res => {
        if (key) {
          if (res.data.length > 0) {
            _this.options = res.data;
          } else {
            _this.openModel();
          }
        } else {
          _this.z_param.changeSite = res.data;
          _this.options = _this.z_param.changeSite;
        }
      });
    },
    initOptions() {
      let _this = this;
      if (_this.z_param.changeSite.length > 0) {
        _this.options = _this.z_param.changeSite;
      } else {
        _this.getSite(null);
      }
    },
    query() {
      this.getSite(this.value);
    },
    openModel() {
      this.$message({
        showClose: true,
        message: '没有查到当前网点对象！',
        type: 'warning',
        duration: 5000,
      });
    },
    changeSite(id) {
      //切换网点click function
      window.$$global.changeSite(id)
    },
  },
  watch: {
    visible: function(newVal, oldVal) {
      this.dialogVisible = newVal;
      if (this.isFirstEnter) {
        this.isFirstEnter = false;
        this.initOptions();
      }
    },
  },
  mounted() {},
};
</script>

