<template>
  <div class="menulist" @click="openMenus()">
    <el-menu
      :default-active="activeTab.key"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="isCollapse"
      :default-openeds="defaultOpen"
      :unique-opened="true"
    >
      <template v-for="(menuOne, i) in menus">
        <el-submenu
          v-if="menuOne.subMenus && menuOne.subMenus.length > 0"
          v-bind:index="menuOne.key"
          :keyName="menuOne.key"
          :index="menuOne.key"
          :show-timeout="100"
          :hide-timeout="100"
          :key="i"
        >
          <template   slot="title">
            <div :class="menuOne.cssName">
              <i
                :class="{
                'el-icon-plus': !(menuOne.key == activeFirst),
                'el-icon-minus': menuOne.key == activeFirst,
              }"
              ></i>
              <span v-text="menuOne.name"></span>
            </div>

          </template>
          <template v-for="(twoMenu, index) in menuOne.subMenus">
            <el-menu-item
              :style="{color: twoMenu.important ? '#d63434' : '#a7b4c3'}"
              :index="twoMenu.key"
              @click.native.prevent.stop="excuteRouter(twoMenu.key)"
              v-text="twoMenu.important ? '※'+twoMenu.title : twoMenu.title"
              :class="twoMenu.path.toString().substring(1)+'Tour'"
              :key="index"
            ></el-menu-item>
          </template>
        </el-submenu>
      </template>
    </el-menu>
    <el-radio-group v-model="isCollapse">
      <el-radio-button :label="false" v-show="isCollapse"
      ><i class="el-icon-d-arrow-right"></i
      ></el-radio-button>
      <el-radio-button :label="true" v-show="!isCollapse"
      ><i class="el-icon-d-arrow-left"></i
      ></el-radio-button>
    </el-radio-group>
  </div>
</template>
<script>
  import {mapGetters, mapActions} from 'vuex';

  export default {
    data() {
      return {
        isCollapse: false,
        defaultOpen:['1'],
      };
    },
    computed: {
      ...mapGetters(['navTabs', 'activeTab', 'activeFirst', 'menus']),
    },
    methods: {
      ...mapActions([
        'setActiveTab',
        'setactiveFirst',
        'changeCollapse',
        'setTabClose',
        'setKeepRoutes',
      ]),
      excuteRouter(key) {
        this.$store.commit('setTabClose', false);
        var arry = key.split('-');
        var first = arry[0] - 1,
          second = arry[1] - 1;
        var curentTab = this.menus[first].subMenus[second];

        /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的二级菜单*/
        this.$store.commit('setActiveTab', {active: curentTab});
        /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的一级菜单的展开状态*/
        this.$store.commit('setactiveFirst', first + 1);

        var newTab = this.menus[first].subMenus[second];
        newTab.closable = true;
        var ifHas = {
          status: false,
          index: 0,
        };
        this.navTabs.map(function(navTab, index) {
          if (navTab.key == newTab.key) {
            ifHas.status = true;
            ifHas.index = index;
          }
        });
        if (ifHas.status) {
          // this.navTabs.splice(ifHas.index, 1);
          // this.navTabs.push(newTab);
        } else {
          this.navTabs.push(newTab);
        }
        if (this.navTabs.length > 5) {
          this.navTabs.splice(0, 1);
        }
        let routes = this.navTabs.map(item => item.path.slice(1));
        this.$store.commit('setKeepRoutes', routes);
        this.$emit("tourChange")
      },
      openMenus() {
        let target = event.target || event.srcElement;
        if (
          target.className == 'el-submenu__title' ||
          target.nodeName.toLowerCase() == 'i'
        ) {
          if (this.isCollapse) {
            let keyNameNode =
              target.parentNode.nodeName.toLowerCase() == 'li'
                ? target.parentNode
                : target.parentNode.parentNode;
            if (keyNameNode.nodeName.toLowerCase() == 'li') {
              // console.log(keyNameNode.getAttribute("keyName"));
              let firsKeyName = keyNameNode.getAttribute('keyName');
              // console.log(firsKeyName);
              this.$store.commit('setactiveFirst', firsKeyName);
            } else {
              this.$store.commit('setactiveFirst', 0);
            }
            this.isCollapse = false;
          }
        }
      },
      handleOpen(index, indexPath) {
        /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的一级菜单的展开状态*/
        // this.$store.commit('setactiveFirst', indexPath);
        // console.log('展开='+indexPath);
      },
      handleClose(index, indexPath) {
        // this.$store.commit('setactiveFirst', -1);
        // console.log('close='+indexPath);
      }
    },
    mounted() {
      /**初始化界面导航active显示*/
      var activeName = window.location.href; //获取当前界面完整的网址
      var tempUrl = window.location.origin; //获取网页origin，后面拼接路由名称
      var objSub = null;
      this.menus.forEach(function(item, index) {
        // console.log(item)
        var submenus = item.subMenus;
        submenus.forEach(function(sub, second) {
          if (activeName.indexOf(sub.path) > -1) {
            objSub = sub;
          }
        });
      });
      // if(!objSub){
      //   objSub = this.menus[0].subMenus[0];
      // }
      // console.log(objSub);
      if (objSub) {
        //如果有/#/后面有值，没有，不进行跳转
        // console.log(objSub);

        this.excuteRouter(objSub.key);
      }
    },
    watch: {
      isCollapse: function(val, oldVal) {
        this.$store.commit('changeCollapse', val);
        this.$nextTick(() => {
          setTimeout(() => {
            this.z_method.refreshSpread();
          }, 1000);
        });
      },
      $route: function(n, o) {

        var activeName = window.location.href; //获取当前界面完整的网址
        var tempUrl = window.location.origin; //获取网页origin，后面拼接路由名称
        var objSub = {key:false};
        this.menus.forEach(function(item, index) {
          var submenus = item.subMenus;
          submenus.forEach(function(sub, second) {
            if (activeName.indexOf(sub.path) > -1) {
              objSub = sub;
            }
          });
        });
        let checkh = this.navTabs.filter(res => {
          return res.key == objSub.key;
        });
        if(checkh.length === 0){
          if (objSub.key) {
            this.excuteRouter(objSub.key);
          }
        }else{
          const curentTab = objSub;
          /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的二级菜单*/
          this.$store.commit("setActiveTab", {active: curentTab});
          const activeNum = parseInt(curentTab.key.split("-")[0]);
          /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的一级菜单的展开状态*/
          this.$store.commit("setactiveFirst", activeNum);
        }
      }
    },
  };
</script>

