<template>
    <main class="verification">
        <el-dialog
            :modal-append-to-body="false"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
            :show-close="false"
            :visible.sync="dialogVisibles"
            width="400px">
            <div class="dialog-body">
                <div class="dialog-title">
                    <i class="el-icon-error"></i>
                    <div>{{titles}}</div>
                </div>
                <div class="dialog-con">{{titleCon}}</div>
                <div class="dialog-inp">
                    <p><span class="c-r">*</span>输入确认码</p>
                    <el-input v-model.trim="varInput" maxlength="3" placeholder="请输入" ref="inputref"></el-input>
                    <div class="veri">{{ randomNum }}</div>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleClose">取 消</el-button>
                <el-button type="primary" @click="handleAct" :disabled="varInput.length == 0">确 定</el-button>
            </span>
        </el-dialog>
    </main>
</template>
<script>
export default {
  name: 'verification',
  props: {
    dialogVisibles: {
      type: Boolean,
      default: false
    },
    titles: {
      type: [Number, String],
      default: '是否确认删除该报价组？'
    },
    titleCon: {
      type: [Number, String],
      default: '注意待删除的报价组还存在客户哦'
    }
  },
  data() {
    return {
      randomNum: 111,
      varInput: ''
    }
  },
  watch: {
    // 监听，是否显示
    dialogVisibles(value, oldValue) {
      this.randomNumA()
      this.$nextTick(() => {
        this.$refs.inputref.focus()
      })
    }
  },
  methods: {
    // 提示消息
    msg(val, type = 'warning') {
      this.$message({
        showClose: true,
        message: val,
        type: type,
        duration: 2000
      })
    },
    // 验证输入
    handleAct() {
      this.$refs.inputref.focus()
      if (this.varInput == '') {
        this.msg('请输入确认码!')
        return
      } else if (this.varInput !== this.randomNum) {
        this.varInput = ''
        this.msg('确认码有误!')
        this.randomNumA()
        return
      } else {
        this.varInput = ''
        this.$emit('callBack', true)
      }
    },
    // 关闭内容
    handleClose(done) {
      this.varInput = ''
      this.$emit('callBack', false)
    },
    // 生成一个随机数
    randomNumA() {
      this.randomNum = Math.floor(Math.random() * (999 - 100) + 100) + ''
    }
  }
}
</script>
