/**
 *head 导航栏历史记录mennus
 **/
const state = {
  navTabs: [
    // {
    // 	title:'神州必读',
    //           key:'1-1',
    //           path:'/read',
    // 	closable:false //welcome 页面不支持关闭
    // }
  ],
  activeTab: {
    // key:'1-1',
    // title:'神州必读'
  },
  activeFirst: 0,
  collapse: false, //是否展开菜单栏
  loading: false, //全局loading
  tabClose: false, //顶部tab是否点击了关闭
  keepRoutes: [], // 缓存的路由
};
const getters = {
  navTabs(state) {
    return state.navTabs;
  },
  activeTab(state) {
    return state.activeTab;
  },
  activeFirst(state) {
    return state.activeFirst;
  },
  collapse(state) {
    return state.collapse;
  },
  loading(state) {
    return state.loading;
  },
  tabClose(state) {
    return state.tabClose;
  },
  keepRoutes(state) {
    return state.keepRoutes;
  }
};
const mutations = {
  setActiveTab(state, { active }) {
    state.activeTab = active;
  },
  setactiveFirst(state, num) {
    state.activeFirst = num;
  },
  changeCollapse(state, status) {
    state.collapse = status;
  },
  setLoading(state, status) {
    state.loading = status;
  },
  setTabClose(state, tabClose) {
    state.tabClose = tabClose;
  },
  setKeepRoutes(state, routes) {
    state.keepRoutes = routes;
  }
};
export default {
  state,
  getters,
  mutations
};
