<template>
  <div class="expand-collapse">
    <el-collapse-transition>
      <div
        v-show="isCollapse"
        ref="collapse"
      >
        <slot name="collapse" />
      </div>
    </el-collapse-transition>
    <div class="collapse-btn">
      <div>
        <span @click="onChange">
          <i :class="isCollapse?'el-icon-arrow-up':'el-icon-arrow-down'"></i>
          {{isCollapse ? '收起':'展开查询'}}
          <i :class="isCollapse?'el-icon-arrow-up':'el-icon-arrow-down'"></i></span>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * value 会将变化的高度传递出去，减去这个高度就好
 */
export default {
  props: {
    value: [String, Number]
  },
  data() {
    return {
      isCollapse: true
    };
  },
  methods: {
    onChange() {
      let height = this.$refs.collapse.clientHeight;
      this.isCollapse = !this.isCollapse;
      this.$emit("input", height);
      this.$emit("change", height);
    }
  }
};
</script>

