<template>
  <div class="import-box">
    <input id="excel-upload-input" ref="excel-upload-input" type="file" accept=".xlsx, .xls,.csv" class="hide" @change="handleFileChange">
    <el-dialog
      title="批量导入"
      :modal-append-to-body="false"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :width="width + 'px'"
      :before-close="handleClose">
      <div class="batch-content-box" :style="{width: width ? width + 'px' : '700px',height: height ? height + 'px' : '500px'}">
        <div class="content-center">
          <div class="line">
            <el-progress :text-inside="true" :stroke-width="14" :percentage="progress" status="success"></el-progress>
          </div>
          <div class="width-full">
            <div class="scroll-table-box">
              <table class="table-head">
                <tr>
                  <td v-for="(item,index) in columns" :key="index" :style="{width: item.size + 'px'}">{{item.displayName}}</td>
                  <td style="width: 250px">处理结果</td>
                </tr>
              </table>
              <div :style="{height: (height - 174) + 'px'}" class="table-body-box">
                <table class="table-body">
                  <tr v-for="(item,index) in tableData" :key="index">
                    <td v-for="(column,i) in columns" :key="i" :style="{width: column.size + 'px'}">{{item[column.name]}}</td>
                    <td style="width: 250px">
                      <div v-if="item.resultCode===0">
                        <i class="el-icon-success"></i>
                        <span>成功</span>
                      </div>
                      <i class="el-icon-loading" v-if="item.importStatus===1"></i>
                      <div v-if="item.importStatus > 1 && item.resultCode!==0" class="error-msg">
                        <i class="el-icon-error"></i>
                        <span :title="item.msg">{{item.msg}}</span>
                      </div>
                    </td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
          <p class="tip-write">共{{totalNum}}条，成功{{successNum}}条，失败{{failedNum}}条，{{totalNum-successNum-failedNum}}条未处理</p>
        </div>
        <div class="content-bottom">
          <el-button type="success" class="batch-btn" @click="startNew" v-show="status===0">开始</el-button>
          <el-button type="success" class="batch-btn" @click="continueBatch" v-show="status===2">继续</el-button>
          <el-button type="danger" style="width: 95px" @click=suspendBatch v-show="status===1">暂停</el-button>
          <el-button type="success" class="batch-btn" @click="handleClose" v-show="status===3">{{isErrorData? '已结束' : status===3 && totalNum === successNum ? '已结束' : '重新编辑失败条目'}}</el-button>
          <i class="el-icon-download" v-show="status===3 && totalNum !== successNum && isErrorData" style="color:red;margin-left: 24px;cursor: pointer;" @click="downloadError">下载失败数据</i>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {get,post} from '@/config/axios.config'
  import { cloneDeep } from 'lodash'
  import moment from 'moment'
  import XLSX from 'xlsx'
  export default {
    name: "sz-import",
    props: {
      dataValidate: {
        type: Function,
        required: true,
      },
      columns: {
        type: Array,
        validator: function(cl) {
          return cl.length > 0;
        },
        default: () => {
          return [];
        },
      },
      width: {
        type: [Number, String],
        default: 800
      },
      height: {
        type: Number,
        default: 500
      },
      batchUrl: {
        type: String,
        default: "",
        required: true
      },
      updateData: {
        type: Array,
        default: () => {
          return []
        }
      },
      maximum: {
        type: Number,
        default: 2000
      },
      isErrorData:{
        type: Boolean,
        default: false
      }
    },
    computed: {
      progress(){
        return parseInt((this.successNum + this.failedNum) * 100 /this.totalNum) || 0;
      }
    },
    data (){
      return {
        dialogVisible: false,
        totalNum: 0,
        successNum: 0,
        failedNum: 0,
        status: 0,
        count: 0,
        tableData: [],
        updateTime: moment().format('YYYY-MM-DD HH:mm:ss')
      }
    },
    methods: {
      downloadError(){
        let errorData = this.tableData.filter(item => {
          return item.msg;
        });
        this.$emit("errorData", errorData);
      },
      handleClose() {
        if(this.status !== 0){
          this.$emit("model", this.tableData);
          this.handleClose2();
          this.dialogVisible = false;
        }else if(this.status === 0){
          this.$confirm('此操作将停止执行, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            // this.$emit("model", this.tableData);
            this.handleClose2();
            this.dialogVisible = false;
          }).catch(() => {});
        }
      },
      startNew(){
        this.updateTime = moment().format('YYYY-MM-DD HH:mm:ss');
        this.status = 1;
        if(this.tableData && this.tableData.length > 0){
          let row = cloneDeep(this.tableData[this.count]);
          this.oneRow(row)
        }
      },
      oneRow(param){
        this.$set(this.tableData[this.count],'importStatus',1);
        this.tableData[this.count]["updateTime"] = this.updateTime;
        this.dataValidate(param).then(row => {
          post(this.batchUrl,row,false,false).then(res => {
            this.$set(this.tableData[this.count],'importStatus',2);
            this.$set(this.tableData[this.count],'resultCode',res.code);
            if(res.code === 0){
              this.successNum++;
            }else{
              this.failedNum++;
              this.$set(this.tableData[this.count],'msg',res.message);
            }
            if(this.tableData.length > this.count + 1){
              if(this.status === 1){
                this.callBack()
              }
            }else{
              this.status = 3;
            }
          }).catch(r => {
            let msg = '失败';
            if(r.message) msg = r.message;
            this.importError(msg)
          });
        }).catch(e => {
          this.importError(e)
        });
      },
      importError(error){
        try {
          this.$set(this.tableData[this.count],'msg',error);
        }
        finally {
          this.$set(this.tableData[this.count],'importStatus',2);
          this.failedNum++;
          if(this.tableData.length > this.count + 1){
            if(this.status === 1){
              this.callBack()
            }
          }else{
            this.status = 3;
          }
        }
      },
      callBack(){
        this.count++;
        let queryParam = cloneDeep(this.tableData[this.count]);
        this.oneRow(queryParam)
      },
      suspendBatch(){
        this.status = 2;
      },
      continueBatch(){
        this.status = 1;
        this.callBack()
      },
      handleClose2() {
        this.successNum = 0;
        this.failedNum = 0;
        this.count = 0;
        this.status = 0;
        this.totalNum = 0;
        this.tableData = [];
      },
      import() {
        document.getElementById('excel-upload-input').click();
      },
      handleFileChange(e){
        const files = e.target.files;
        const itemFile = files[0];
        if (!itemFile) return;
        if(itemFile.size > 1048576){
          this.$message({
            message: '文件大小不能超过1M',
            type: 'warning',
            showClose: true,
          });
          if(this.$refs['excel-upload-input'].value){
            this.$refs['excel-upload-input'].value = null; // fix can't select the same excel
          }
          return
        }
        this.$store.commit('setLoading', true);
        setTimeout(() => {
          this.readFile(itemFile);
          if(this.$refs['excel-upload-input'].value){
            this.$refs['excel-upload-input'].value = null; // fix can't select the same excel
          }
        }, 100);
      },
      readFile(file) {
        try {
          const reader = new FileReader();
          reader.onload = e => {
            const data = e.target.result;
            const fixedData = this._fixData(data);
            const workbook = XLSX.read(btoa(fixedData), {type: 'base64'});
            const firstSheetName = workbook.SheetNames[0];
            const worksheet = workbook.Sheets[firstSheetName];
            const header = this._getHeaderRow(worksheet);
            const results = XLSX.utils.sheet_to_json(worksheet);
            this._generateData(header, results);
          };
          reader.onloadend = () => {
            this.$store.commit('setLoading', false);
          };
          reader.readAsArrayBuffer(file);
        } finally {
        }
      },
      _fixData(data) {
        let o = '';
        let l = 0;
        const w = 10240;
        for (; l < data.byteLength / w; ++l)
          o += String.fromCharCode.apply(
            null,
            new Uint8Array(data.slice(l * w, l * w + w))
          );
        o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
        return o;
      },
      _getHeaderRow(sheet) {
        const headers = [];
        const range = XLSX.utils.decode_range(sheet['!ref']);
        let C;
        const R = range.s.r;
        /* start in the first row */
        for (C = range.s.c; C <= range.e.c; ++C) {
          /* walk every column in the range */
          var cell = sheet[XLSX.utils.encode_cell({c: C, r: R})];
          /* find the cell in the first row */
          var hdr = 'UNKNOWN ' + C; // <-- replace with your desired default
          if (cell && cell.t) hdr = XLSX.utils.format_cell(cell);
          headers.push(hdr);
        }
        return headers;
      },
      _generateData(header, results) {
        if (this._validateHeader(header) && this._validateIsNull(results)) {
          this._packageData(results);
        }
      },
      _validateHeader(hd) {
        if (hd) {
          for (let i = 0; i < this.columns.length; i++) {
            let column = this.columns[i];
            let title = hd['' + i];
            if (!title || column.displayName !== title) {
              this.$message({
                showClose: true,
                message: '导入文件格式有误，请重新选择',
                type: 'warning'
              });
              return false;
            }
          }
          return true;
        } else {
          this.$message({
            showClose: true,
            message: '导入文件格式有误，请重新选择',
            type: 'warning'
          });
          return false;
        }
      },
      _validateIsNull(rt) {
        if (rt.length === 0) {
          this.$message({
            showClose: true,
            message: '导入文件没有数据，请重新选择',
            type: 'warning'
          });
          return false;
        }
        if (rt.length > this.maximum) {
          this.$message({
            showClose: true,
            message: '导入表格数据不可超过'+this.maximum+'个',
            type: 'warning'
          });
          return false;
        }
        return true;
      },
      _packageData(rt) {
        let arr = [];
        for (let i = 0; i < rt.length; i++) {
          let row = rt['' + i];
          let obj = {};
          let hasValue = false;
          for (let colIndex = 0; colIndex < this.columns.length; colIndex++) {
            let col = this.columns[colIndex];
            let colValue = row['' + col.displayName];
            if (colValue !== undefined && colValue + '' !== '') {
              hasValue = true;
              obj[col.name] = colValue;
            }
          }
          if (hasValue) {
            arr.push(obj);
          }
        }
        if (arr.length > 0) {
          this.successNum = 0;
          this.failed = 0;
          this.count = 0;
          this.status = 0;
          this.tableData = arr;
          this.dialogVisible = true;
          this.totalNum = arr.length;
        }
      },
    },
    watch: {
      updateData(newVal, oldVal){
        if(newVal.length > 0) {
          this.tableData = cloneDeep(newVal);
          this.tableData.forEach(item => {
            item.importStatus = 0;
          });
          this.dialogVisible = true;
          this.totalNum = newVal.length;
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .import-box{
    width: 0;
    height: 0;
    .hide{
      width: 0;
      height: 0;
    }
    .batch-content-box{
      background: #fff;
      .content-center{
        padding: 10px 10px 0;
        font-size: 13px;
        .line{
          margin: 2px 0 12px;
        }
        .width-full{
          width: 100%;
          overflow: auto;
          .scroll-table-box{
            display: inline-block;
            .table-head{
              td{
                height: 16px;
                text-align: center;
                padding: 10px 0;
                color: #333333;
                background: #eaf1f9;
              }
            }
            .table-body-box{
              overflow: auto;
              .table-body{
                td{
                  text-overflow: ellipsis;
                  overflow: hidden;
                  white-space: nowrap;
                  text-align: center;
                  color: #666666;
                  height: 30px;
                  padding: 0;
                  .el-icon-success{
                    color: #4acd9b;
                  }
                  .el-icon-error{
                    color: #fc4270;
                  }
                  .el-icon-loading{
                    color: #2191E5;
                    font-size: 18px;
                  }
                }
                .error-msg{
                  text-overflow: ellipsis;
                  overflow: hidden;
                  white-space: nowrap;
                  width: 240px;
                }
                tr:nth-of-type(2n){
                  background: #f0f7fd;
                }
                tr:hover{
                  background: #fff4f0;
                }
              }
            }
          }
        }
        table{
          width: 100%;
          border-collapse: collapse;
          table-layout: fixed;
        }
        .tip-write{
          color: #e35664;
          font-size: 12px;
          text-align: right;
          margin-top: 16px;
        }
      }
      .content-bottom{
        height: 50px;
        line-height: 50px;
        text-align: center;
      }
    }
  }

</style>

<style>
  .import-box .el-dialog__wrapper .el-dialog .el-dialog__body{
    padding: 0!important;
  }
</style>
