<template>
  <div class="down-load">
    <el-dialog
      title="下载进度"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      :before-close="clear"
      :visible.sync="dialogVisible"
      :modal-append-to-body="false"
      width="30%"
      center
    >
      <div class="bar-mod">
        <el-progress
          v-if="successStatus === 'text'"
          :percentage="progress"
          :stroke-width="16"
        ></el-progress>
        <el-progress
          v-else
          :percentage="progress"
          :status="successStatus"
          :stroke-width="16"
        ></el-progress>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// isPercent  是否显示百分号， 配好表格组件使用
// iShow 控制这一列是否需要导出
// isConversionComma  是否转换英文逗号为中文逗号，防止导出的时候回新增一行
export default {
  props: {
    visible: Boolean,
    head: Object,
    paramData: Array,
    sum: Number,
    current: Number,
    fileName: String,
    isDynamicHeade: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      dialogVisible: false,
      sumSecond: this.sum, //一共需要获取多少次数据
      curentSecond: this.current, //当前第几次获取数据
      successStatus: "text", //进度条的状态
      orgHead: null, //克隆传入的head数据
      orgData: null, //克隆传入的data 数据
      tableHead: null, //导出标题字符串
      dataSource: null //导出数据字符串
    };
  },
  computed: {
    progress() {
      let percent = parseInt((this.curentSecond / this.sumSecond) * 100);
      return percent;
    }
  },
  mounted() {},
  methods: {
    toStringHead: function(columns) {
      //将表格标题拼接成"标题1，标题2，。。。"的字符串
      let head = "";
      columns.forEach(function(item, index) {
        if (item.displayName != "选择") {
          if (item.iShow != false) {
            //隐藏当前数据
            if (index == columns.length - 1) {
              head += item.displayName + "\n";
            } else {
              head += item.displayName + ",";
            }
          }
        }
      });
      if (columns[columns.length - 1].iShow == false) {
        head = head.substring(0, head.length - 1) + "\n";
      }
      this.tableHead = head;
    },
    toStringData: function(tableList, number) {
      //将表格数据拼接成"数据1，数据2，。。。\n"的字符串
      let _this = this;
      if (number === 1) {
        //若果传进来是第一次数据，置空_this.dataSource = ''
        _this.dataSource = "";
      }
      tableList.forEach(function(list, index) {
        if (list.remark && list.remark != null) {
          list.remark = list.remark.replace(/[\n]/gi, ";"); //把用户输入的回车转换成分号
        }

        _this.head.columns.forEach(function(item, second) {
          if (
            item.formatter &&
            (item.formatter === "yyyy-MM-dd" || item.formatter == "yyyy/MM/dd")
          ) {
            tableList[index][item.name] = tableList[index][item.name]
              ? _this
                  .moment(new Date(tableList[index][item.name]))
                  .format("YYYY-MM-DD") + "\t"
              : "";
          }
          if (item.formatter && item.formatter == "yyyy-MM-dd hh:mm:ss") {
            tableList[index][item.name] = tableList[index][item.name]
              ? _this
                  .moment(new Date(tableList[index][item.name]))
                  .format("YYYY-MM-DD HH:mm:ss") + "\t"
              : "";
          }
          if (item.value && typeof item.value == "function") {
            // 根据表格head colums里面的value function 返回显示的在界面上的数据
            tableList[index][item.name] = item.value(tableList[index], _this);
          }
          // 防止数据为零的时候，表格没有数据显示  isPercent
          if (item.isCode && item.isPercent === true) {
            if (!isNaN(tableList[index][item.name])) {
              tableList[index][item.name] = `${tableList[index][item.name]}%`;
            } else {
              tableList[index][item.name] = ``;
            }
          } else if (typeof tableList[index][item.name] == "undefined") {
            tableList[index][item.name] = "";
          } else if (
            item.isConversionComma &&
            item.isConversionComma === true 
          ) {
            if( tableList[index][item.name] == null) {
              tableList[index][item.name] = "";
            }else{
              tableList[index][item.name] = tableList[index][item.name].replace(
              /\,/g,
              "，"
            );
            }
            
            if (tableList[index][item.name].indexOf("，") == -1) {
              tableList[index][item.name] = `="${tableList[index][item.name]}"`;
            }
          } else if (item.isToolTip) {
            if (tableList[index][item.name] && tableList[index][item.name] != null) {
              tableList[index][item.name] = tableList[index][item.name].replace(
                ",",
                "，"
              );
              return;
            }
            tableList[index][item.name] = "";
          } else if (item.isCode && item.isCode === true) {
            tableList[index][item.name] = `="${tableList[index][item.name]}"`;
          } else if (
            tableList[index][item.name] === 0 ||
            tableList[index][item.name] == "0"
          ) {
            tableList[index][item.name] = 0;
          } else if (!tableList[index][item.name]) {
            //没有值就进来
            tableList[index][item.name] = "";
          } else {
            tableList[index][item.name] = tableList[index][item.name];
          }
          if (item.displayName != "选择") {
            if (item.iShow != false) {
              //隐藏当前数据
              // 只拼接 表头不是选择的列
              if (second == _this.head.columns.length - 1) {
                _this.dataSource += tableList[index][item.name] + "\n";
              } else {
                _this.dataSource += tableList[index][item.name] + ",";
              }
            }
          }
        });
        if (_this.head.columns[_this.head.columns.length - 1].iShow == false) {
          _this.dataSource =
            _this.dataSource.substring(0, _this.dataSource.length - 1) + "\n";
        }
      });
    },
    comonToSring(head, data, index) {
      let _this = this;
      let tempHead = JSON.stringify(head), // 复制传进来的head data数据，不修改父组件中的原数据
        tempParamData = JSON.stringify(data);

      _this.orgHead = JSON.parse(tempHead);
      _this.orgData = JSON.parse(tempParamData);
      if (head && head.columns && head.columns.length > 0) {
        if (index == 1) {
          //如果是第一次获取数据，需要转换表格标题栏
          _this.toStringHead(_this.orgHead.columns);
        }
        if (this.isDynamicHeade) {
          //如果是动态表头 需要再次转换表头
          _this.toStringHead(_this.orgHead.columns);
        }
        _this.toStringData(_this.orgData, index);
        // let b1 = new Blob([_this.dataSource])
        // console.log(this.sumSecond, this.curentSecond);

        if (_this.sumSecond == _this.curentSecond) {
          //如果当前获取数据的index == 获取数据的总次数，则数据获取完成，经数据导出
          _this.successStatus = "success"; //改变进度条状态
          _this.exportExcel(_this.tableHead, _this.dataSource); //导出数据

          setTimeout(function() {
            // 3s后关闭弹窗
            _this.clear();
          }, 1000);
        }
      } else {
        console.log("表格标题栏不存在，不能导出");
      }
    },
    exportExcel(head, data) {
      let str = head + data;
      let fileName = this.fileName;
      let exportContent = "\ufeff";
      let blob = new Blob([exportContent + str], {
        type: "text/csv;charset=utf-8"
      });
      let time = new Date()
        .toLocaleString("chinese", { hour12: false })
        .replace(/[/]/gi, "");
      let newTime = time.replace(/:/gi, "");
      this.fileSaver.saveAs(blob, fileName + newTime + ".csv");
    },
    clear() {
      this.dialogVisible = false;
      this.successStatus = "text"; //改变进度条状态
      this.$emit("clear", false);
    }
  },
  watch: {
    visible(val, old) {
      //监听父组件的visible数值的变化
      let _this = this;
      if (val) {
        this.dialogVisible = true; //打开弹窗
        _this.comonToSring(_this.head, _this.paramData, _this.curentSecond); //转换数据,d导出表格
      }
    },
    sum(val, old) {
      //监听传入的获取数据的总次数的变化
      this.sumSecond = val;
    },
    current(val, old) {
      // console.log("csv-------", val);
      //监听传入的获取数据当前第几次的变化
      let _this = this;
      _this.curentSecond = val;
      if (this.dialogVisible) {
        //弹窗打开状态
        _this.comonToSring(_this.head, _this.paramData, _this.curentSecond); //转换数据,导出表格
      }
    },
    deep: true
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bar-mod {
  width: 100%;
  padding: 30px 0px;
}
</style>
<style>
.down-load .el-dialog .el-dialog__header,
.el-dialog__footer {
  height: 35px;
}

.down-load .el-dialog__headerbtn {
  top: 10px;
}

.down-load .el-progress__text {
  position: absolute;
  right: 0px;
  font-size: 14px !important;
}

.down-load .el-progress__text .el-icon-circle-check {
  font-size: 18.6px !important;
  position: relative;
  right: 10px;
}
</style>
