<template>
  <div class="sz-settlement-component del-arrow-up">
    <el-select
      ref="szSettlementSelect"
      v-model="inputValue"
      :clearable="clearable"
      :filterable="true"
      :filter-method="remote"
      :disabled="disabled"
      @change="changeItem"
      @clear="clear"
      @blur="blur"
      no-match-text=" "
      :placeholder="placeholder"
      :popperClass="popperClass"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.name+'  '+item.code"
        :value="item.id"
      />
    </el-select>
  </div>
</template>

<script>
  import { getSettlementAjax } from "@/config/api";
  export default {
    name: "sz-settlement",
    props: {
      value: {
        type: [Number, String]
      },
      default: [Number, String],
      clearable: {
        type: Boolean,
        default: true
      },
      placeholder: {
        type: String,
        default: ""
      },
      popperClass: {
        //acconunt-name-class
        type: String,
        default: ""
      },
      types: {
        type: Array,
        default: () => []
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        inputValue: this.value,
        options: [],
        globalOptions: []
      };
    },
    mounted() {
      this.getSettlement();
    },
    methods: {
      // 远程搜索方法
      remote: _.debounce(function(key) {
        this.getSettlement(key);
      }, 1000),
      // 获取结算对象
      getSettlement(key) {
        let options = {
          name: key,
          types: this.types.join(",")
        };
        getSettlementAjax(options)
          .then(res => {
            if (res.data && res.data.length > 0) {
              this.options = res.data;
              if (this.globalOptions.length === 0) {
                this.globalOptions = res.data;
              }
            } else {
              // this.msg("没有查询到数据");
              this.options = [];
            }
          })
          .catch(response => {});
      },
      // 将得到的值抛出去
      changeItem(key) {
        let tempObj = this.options.filter(function(ele) {
          return ele.id == key ? true : false;
        });
        let chooseObj = tempObj[0] ? tempObj[0] : null;
        this.$emit("input", this.inputValue);
        this.$emit("model", this.chooseObj);
      },
      chooseOne(key, keyIds, callBack) {
        let options = {
          name: key,
          types: this.types.join(",")
        };
        getSettlementAjax(options).then(res => {
          if (res.data && res.data.length > 0) {
            this.options.push(...res.data);
            this.options = this.options.concat(this.options);
            let hash = {};
            this.options = this.options.reduce((item, next) => {
              if (!hash[next.id]) {
                hash[next.id] = true;
                item.push(next);
              }
              return item;
            }, []);
            let tempObj = this.options.filter(function(ele) {
              return ele.id == keyIds ? true : false;
            });
            let chooseObj = tempObj[0] ? tempObj[0] : null;
            if (chooseObj == null) {
              this.$emit("input", "");
              this.inputValue = "";
            } else {
              this.$emit("input", keyIds);
              this.inputValue = keyIds;
            }
          }
          callBack(self, this.inputValue);
        });
      },
      blur() {
        if (this.options.length === 0) {
          setTimeout(() => {
            this.options = this.globalOptions;
          }, 1000);
        }
      },
      autoBlur() {
        if (this.$refs.szSettlementSelect) this.$refs.szSettlementSelect.visible = false;
      },
      clear() {
        this.inputValue = "";
        this.getSettlement();
      },
      msg(val) {
        this.$message({
          showClose: true,
          message: val,
          type: "warning",
          duration: 3000
        });
      }
    },
    watch: {
      value(val, oldVal) {
        this.inputValue = val;
        this.$emit("input", val);
      }
    }
  }
</script>

