<script>
import {mapGetters, mapActions} from 'vuex';
import {getVersion, getUnloadNum} from '../../config/api';
import changeSite from './changeSite';
import {del,get} from '@/config/axios.config'
export default {
  components: {
    changeSite,
  },
  data() {
    return {
      unloadNum: 0,
      visible: false,
      isClear: false,
      sysCode: '',
      versionAddress: 'javaScript:;',
    };
  },
  mounted() {
    // let host2 = `${location.origin}`;
    // this.versionAddress = `${host2.replace(
    //   'bills',
    //   ''
    // )}/portal/version.html?code=bills`;
    // let options = {
    //   sysCode: 'bills',
    // };
    // getVersion(options).then(res => {
    //   this.sysCode = res.data;
    // });
    getUnloadNum({})
      .then(res => {
        this.unloadNum = res.data;
        // 工作台传参
        if (self != top) {
          const lt = window.location;
          // console.log(window.parent.href, 'qwertyh');
          // 页面传参
          window.parent.postMessage({unloadNum: res.data}, '*');
        }
      })
      .catch(() => {});
  },
  computed: {
    ...mapGetters(['navTabs', 'activeTab', 'userPerson', 'tabClose', 'info']),
  },
  methods: {
    ...mapActions([
      'setActiveTab',
      'setactiveFirst',
      'setIframeUrl',
      'setTabClose',
    ]),
    clear(status) {
      //监听关闭切换网点弹窗
      this.visible = status;
    },
    opens() {
      window.open('/task/#/downloadAndComputer');
    },
    deleteSession() {
      window.$$global.deleteSession(del,get)
    },
    updateTabs() {
      console.log(66);
      const target = event.target || event.srcElement;
      const clickDom = target.nodeName.toLowerCase();

      const clickDomClass = target.getAttribute('class');
      const item = target.getAttribute('item');
      this.$store.commit('setTabClose', false);

      if (
        clickDom == 'i' &&
        clickDomClass.indexOf('el-icon-circle-close') > -1
      ) {
        // console.log("clickDom "+ clickDom)
        this.$store.commit('setTabClose', true);
        this.moveTab(item);
      }
      if (clickDom == 'span') {
        const curentTab = this.navTabs[item];
        /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的二级菜单*/
        this.$store.commit('setActiveTab', {active: curentTab});
        const activeNum = parseInt(curentTab.key.split('-')[0]);
        /** 调用store tabs中的setActiveTab 中的方法，更新当前active状态的一级菜单的展开状态*/
        this.$store.commit('setactiveFirst', activeNum);
      }

      let routes = this.navTabs.map(item => item.path.slice(1));
      this.$store.commit('setKeepRoutes', routes);
    },
    goTab(path) {
      //路由跳转
      console.log("路由跳转")
      console.log(path)
      this.$router.push({path: path});
    },
    moveTab(index) {
      if (this.navTabs[index].path == this.activeTab.path) {
        let curentTab = {
          key: null,
          title: null,
        };
        this.$store.commit('setActiveTab', {active: curentTab});
      }
      this.navTabs.splice(index, 1);
    },
  },
  watch: {
    activeTab: {
      handler: function(val, oldVal) {
        console.log("activeTab  handler")
        console.log(val)
        if (val.url) {
          //点击的菜单对象如果有url，将该对象的url更新到tab store中，在iframe.vue中调用
          this.$store.commit('setIframeUrl', val.url);
        }
        var path = '/';
        if (!val.key) {
          path = '/null';
        } else {
          this.navTabs.map(function(item, index) {
            if (item.key == val.key) {
              path = item.path;
            }
          });
        }
        this.goTab(path);
      },
      deep: true,
    },
  },
};
</script>

