<template>
  <div class="sz-odd-numbers">
    <div class="left">
      <span
        @click="codeType(0)"
        :class="currentType==1?'active':''"
      >{{nameArr[0]}}</span>
      <span
        :class="currentType===0?'active':''"
        @click="codeType(1)"
        v-if="isSerial"
      >{{nameArr[1]}}</span>
    </div>
    <div class="bill-textarea">
      <el-input
        type="textarea"
        :rows="rows"
        maxlength="20999"
        :placeholder="placeholder"
        @change="onChange"
        v-model="textValue"
        @keydown.space.native="proving"
      />
    </div>
    <div
      class="bill-clear"
      @click="clear"
    >清<br>空</div>
  </div>
</template>

<script>
/**
 * model 绑定的值
 * isSerial 是否需要显示两个
 * codeType(val)事件，流水号类别，1代表单号 2 代表其他
 * nameArr 接收显示的左边显示的文字
 * isComma 是否以逗号分隔单号
 **/
export default {
  props: {
    value: {
      type: String,
      default: ""
    },
    rows: {
      type: Number,
      default: 4
    },
    // 是否有流水号
    isSerial: {
      type: Boolean,
      default: false
    },
    nameArr: {
      type: Array,
      default: () => {
        return ["单号", "流水号"];
      }
    },
    isComma: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      currentType: 0,
      textValue: "",
      isNull:false
    };
  },
  watch: {
    textValue(val,old) {
      if(this.isNull){
        this.textValue = old;
      }else{
        this.textValue = val;
      }
      this.isNull = false;
      // 单号输入 将值传递出去
      // if (this.isComma) {
      //   val = val.replace(/(\r\n)|(\n)/g, ",");
      // }
      // if (val == "") {
      //   this.textValue = "";
      // }
      // this.$emit("input", val);
    }
  },
  computed: {
    placeholder() {
      return `支持多个${this.nameArr[this.currentType]}查询，以回车键隔开！`;
    }
  },
  methods: {
    proving() {
      this.isNull = true;
      // this.textValue = this.textValue.replace(/[^\.\d\n]/g, "");
      // this.textValue = this.textValue.replace(".", "");
    },
    // 单号类型变化
    codeType(val) {
      this.currentType = val;
      this.$emit("codeType", val);
    },
    onChange() {
      let val = this.textValue;
      if (this.isComma) {
        val = val.replace(/(\r\n)|(\n)/g, ",");
      }
      this.$emit("input", val);
    },
    // 清空单号
    clear() {
      this.textValue = "";
      this.$emit("input", this.textValue);
    }
  }
};
</script>
