    align-items: center;
    left: -1px;
  }
  .left {
    width: 22px;
    color: #fff;
    font-size: 12px;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    flex-wrap: wrap;
    background: #33acf9;
    span {
      background: #33acf9;
      flex: 1;
      padding: 5px;
      &.active {
        background: #ccc;
      }
    }
