/**
 * 封装Vue 全局method、param
 * 实例中调用：this.z_method.refreshSpread()
 */
import {
  Message,
  MessageBox
} from 'element-ui';
let z_param = {
  tabH:[],
  data: {},
  hasSubordinate: false,
  siteInfo: {},
  isNode: true,
  admin: false,
  permissions: {},
  city: [],
  customer: [],
  user: [],
  site: [],
  changeSite: [],
  settlement: [],
  avgStatus: false,
  // 单号跳转
  billsUrl: "https://newbill.zt-express.com/index.html?billCode=",
  isActiveH:false
};

let z_method = {
  refreshSpread() {
    setTimeout(function () {
      const divList = document.getElementsByClassName("spread-mod");
      if (divList && divList.length > 0) {
        for (let i = 0; i < divList.length; i++) {
          const spreadRe = GC.Spread.Sheets.findControl(divList[i]);
          spreadRe.refresh();
        }
      }
    }, 3);
  },
  showBtn(key) {
    if (z_param.admin) {
      return true
    } else {
      return !!z_param.permissions[key]
    }
  },
  exportExcel(columns, data, fileName) {
    let formatterDate = function (str) {
      if (String(str).length < 2) {
        return '0' + str
      }
      return str
    };
    let createSpreadRow = function (columns, row) {
      let newRow = {};
      let columnCount = 0;
      columns.map(column => {
        if (column.export !== false) {
          let val = row[column.name];
          if (typeof column.value === 'function') {
            val = column.value(row)
          }
          if (val && column.formatter && column.formatter.match(/yyyy-MM-dd/)) {
            if (toString.call(val) !== '[object Date]') {
              val = new Date(val);
            }
            const year = val.getFullYear();
            const month = formatterDate(val.getMonth() + 1);
            const date = formatterDate(val.getDate());
            const hour = formatterDate(val.getHours());
            const minute = formatterDate(val.getMinutes());
            const second = formatterDate(val.getSeconds());
            if (column.formatter === 'yyyy-MM-dd hh:mm:ss') {
              val = year + '-' + month + '-' + date + ' ' + hour + ':' + minute + ':' + second;
            }
            if (column.formatter === 'yyyy-MM-dd') {
              val = year + '-' + month + '-' + date;
            }
          }
          newRow["" + columnCount] = {
            value: val
          };
          columnCount++
        }
      });
      return newRow;
    };
    const baseJson = {
      "version": "10.1.0",
      "scrollbarMaxAlign": true,
      "tabStripVisible": false,
      "newTabVisible": false,
      "grayAreaBackColor": "#e9eef2",
      "setTabStripRatio": 0.8,
      "sheets": {
        "Sheet1": {
          "name": "Sheet1",
          "defaults": {
            "rowHeight": 26,
            "colWidth": 200,
            "rowHeaderColWidth": 40,
            "colHeaderRowHeight": 30
          },
          "data": {
            "defaultDataNode": {
              "style": {
                "foreColor": "#666666",
                "hAlign": 1,
                "vAlign": 1,
                "font": "12px 'Helvetica Neue', Helvetica, 'Microsoft Yahei', 'Hiragino Sans GB', 'WenQuanYi Micro Hei', sans-serif",
                "themeFont": "Body"
              }
            }
          },
          "colHeaderData": {
            "defaultDataNode": {
              "style": {
                "foreColor": "#666666",
                "font": "12px 'Helvetica Neue', Helvetica, 'Microsoft Yahei', 'Hiragino Sans GB', 'WenQuanYi Micro Hei', sans-serif",
                "themeFont": "Body"
              }
            },
          },
          "selections": {
            "0": {
              "row": 0,
              "rowCount": 1,
              "col": 0,
              "colCount": 1
            },
            "length": 1
          },
          "theme": "Office2007",
          "columns": [],
          "protectionOptions": {
            "allowFilter": true,
            "allowSort": true,
            "allowResizeRows": true,
            "allowResizeColumns": true,
            "allowEditObjects": true
          },
          "index": 0
        }
      }
    };
    //处理列头
    let colHeaderData_dataTable = {};
    let columnCount = 0;
    columns.map(column => {
      if (column.export !== false) {
        colHeaderData_dataTable["" + columnCount] = {
          value: column.displayName
        };
        columnCount++
      }
    });
    baseJson.sheets.Sheet1.columnCount = columnCount;
    baseJson.sheets.Sheet1.colHeaderData.dataTable = {
      "0": colHeaderData_dataTable
    };
    //处理数据
    let data_dataTable = {};
    data_dataTable['0'] = colHeaderData_dataTable;
    let rowCount = 1;
    data.map(row => {
      data_dataTable["" + rowCount] = createSpreadRow(columns, row);
      rowCount++
    });
    baseJson.sheets.Sheet1.rowCount = rowCount;
    baseJson.sheets.Sheet1.data.dataTable = data_dataTable;
    let excelIo = new GC.Spread.Excel.IO();
    let time = new Date()
      .toLocaleString("chinese", {
        hour12: false
      })
      .replace(/[/]/gi, "");
    let newTime = time.replace(/[:\s]/gi, "");
    const file = fileName + newTime + '.xlsx';
    excelIo.save(baseJson, blob => {
      saveAs(blob, file)
    }, e => console.log(e))
  },
  serializeData(data) {
    // If this is not an object, defer to native stringification.
    if (JSON.stringify(data) == '{}') {
      return ((data == null) ? "" : data.toString());
    }
    var buffer = [];
    // Serialize each key in the object.
    for (var name in data) {
      if (!data.hasOwnProperty(name)) {
        continue;
      }
      var value = data[name];
      buffer.push(
        encodeURIComponent(name) + "=" + encodeURIComponent((value == null) ? "" : value)
      );
    }
    // Serialize the buffer and clean it up for transportation.
    var source = buffer.join("&").replace(/%20/g, "+");
    return (source);
  },
  // 下载
  download(url, param) {
    // 锁定下载功能，10秒后恢复，锁定时，直接不处理
    if (z_param.data.exportStatus) {
      Message.success({
        message: '下载太频繁,请稍后!',
        duration: 2800,
        showClose: false,
      });
      return;
    }

    if (!url)
      return;
    if (param) {
      if (url.substring(url.length - 1) != '?')
        url = url + '?';
      url = url + this.serializeData(param);
    }
    // url = this.translateUrl(url);
    var body = document.getElementsByTagName('body')[0];
    if (!body)
      return;
    // 锁定下载功能，10秒后恢复
    z_param.data.exportStatus = true;
    setTimeout(function () {
      z_param.data.exportStatus = false;
    }, 10000);
    var frame = document.getElementById('download_frame');
    if (!frame) {
      frame = document.createElement('iframe');
      frame.id = 'download_frame';
      frame.style.display = 'none';
      frame.src = url;
      body.appendChild(frame);
    } else {
      frame.src = url;
    }
    MessageBox({
      title: '信息',
      message: '已向服务器发送请求,请稍后!',
      center: true,
      confirmButtonClass: "confirmBtn"
    })
    // Message.success({
    //   message: '已向服务器发送请求,请稍后!',
    //   duration: 2800,
    //   showClose: false,
    // });
    // layer.alert('已向服务器发送请求,请稍后!', util.icon_happy);
  },
};

export const zParam = z_param;
export default {
  install(Vue) {
    Vue.prototype.z_method = z_method;
    Vue.prototype.z_param = z_param;
  }
}
