<template>
  <div class="sz-settlement-component del-arrow-up">
    <el-select
      v-model="inputValue"
      :clearable="clearable"
      filterable
      :disabled="disabled"
      @change="changeItem"
      @clear="clear"
      @blur="blur"
      no-match-text=" "
      :placeholder="placeholder"
      :popperClass="popperClass"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.feeName"
        :value="item.id"
      />
    </el-select>
  </div>
</template>

<script>
import { getAccountFee } from "@/config/api";
export default {
  props: {
    value: {
      type: [Number, String]
    },
    default: [Number, String],
    clearable: {
      type: Boolean,
      default: true
    },
    placeholder: {
      type: String,
      default: ""
    },
    popperClass: {
      type: String,
      default: ""//acconunt-name-class
    },
    // 接收网点接口的所有参数值
    param: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      inputValue: this.value,
      options: [],
      loading: false,
      disabled: false
    };
  },
  mounted() {
    this.getSettlement();
  },
  methods: {
    // // 远程搜索方法 :filter-method="remote"
    // remote: _.debounce(function(key) {
    //   this.getSettlement(key);
    // }, 1000),
    // 获取结算对象
    getSettlement(key) {
      getAccountFee().then(res => {
        if (res.data && res.data.length > 0) {
          this.options = this.checkData(res.data);
          this.globalOptions = this.checkData(res.data);
        } else {
          this.options = [];
        }
      }).catch((response) => {});
    },
    checkData(val){
      val = val.filter(ele => {
        return ele.sign === 0;
      });
      return val;
    },
    // 将得到的值抛出去
    changeItem(key) {
      let tempObj = this.options.filter(function(ele) {
        return ele.id == key ? true : false;
      });
      let chooseObj = tempObj[0] ? tempObj[0] : null;
      this.$emit("input", this.inputValue);
      this.$emit("model", this.chooseObj);
    },
    blur() {
      if (this.options.length === 0) {
        setTimeout(() => {
          this.options = this.globalOptions;
        }, 1000);
      }
    },
    clear() {
      this.inputValue = "";
      this.options = this.globalOptions;
    },
    msg(val) {
      this.$message({
        showClose: true,
        message: val,
        type: "warning",
        duration: 5000
      });
    }
  },
  watch: {
    value(value, oldValue) {
      this.inputValue = value;
      this.$emit("input", value);
    }
  }
};
</script>

