<template>
    <el-dialog
        v-drag-dialog
        :title="editTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :modal-append-to-body="false"
        :width="width+'px'"
        center
        class="supplement"
    >
        <div>
            <div style="float:left;padding-left:24px;" v-if="isBills">
                <div class="bill-box" style="height: 200px">
                    <div class="bill-code">
                        <span>单</span>
                        <span>号</span>
                    </div>
                    <el-input
                        type="textarea"
                        class="bill-textarea"
                        :rows="6"
                        resize="none"
                        maxlength="20999"
                        style="width: 130px"
                        placeholder="可输入多个单号，以回车键隔开"
                        @keyup.native="keyup"
                        v-model="billCodeEdit">
                    </el-input>
                    <div class="bill-clear" @click="clearCode">
                        <span>清</span>
                        <span>空</span>
                    </div>
                </div>
            </div>
            <div class="edit">
                <ul>
                    <li>
                        <label>账单类别：</label>
                        <el-select v-model="billsTypeStatusEdit">
                            <el-option
                                v-for=" item in billsTypeStatusOptionsEdit"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            >
                            </el-option>
                        </el-select>
                        <template v-if="billsTypeStatusEdit === 99">
                            <el-checkbox v-model="isCheckedn" style="padding-left:20px;">智能获取结算网点</el-checkbox>
                        </template>
                        <template v-else>
                            <el-checkbox v-model="isCheckedz" style="padding-left:20px;">智能获取结算信息</el-checkbox>
                        </template>
                    </li>
                    <li>
                        <label>账单日期：</label>
                        <el-date-picker 
                            v-model="billTimeEdit" 
                            type="date" 
                            :clearable="false" 
                            :editable="false"
                        />
                        <label v-if='billsTypeStatusEdit ===99'  key="xh"><sup style="color:red;font-size:17px;">*</sup>下级网点：</label>
                        <label v-else key="xh">业务员：</label>
                        <sc-site 
                            :param="{child:true}" 
                            v-model="siteIdEdit" 
                            popper-class="max-index" 
                            v-show="billsTypeStatusEdit === 99" 
                            :disabled="isCheckedn" 
                        />
                        <sc-users 
                            type="2"
                            v-model="userIdEdit"
                            popper-class="max-index" 
                            v-show="billsTypeStatusEdit === 0"
                            :disabled="isCheckedz" 
                        />
                    </li>
                    <li>
                        <label>目的地市：</label>
                        <sc-city 
                            v-model="cityIdEdit"
                            popper-class="max-index" 
                            @change="cityChange"
                        />
                        <label>{{billsTypeStatusEdit === 99?"结算重量：":"客户："}}</label>
                        <sc-users 
                            type="3" 
                            v-model="customerIdEdit" 
                            popper-class="max-index"
                            v-show="billsTypeStatusEdit === 0"
                            :disabled="isCheckedz"
                        />
                        <el-input v-model="weightEdit"  v-show="billsTypeStatusEdit === 99"/>
                    </li>
                    <li>
                        <label>班次类型：</label>
                        <sc-select-multiple
                                v-model="transportEdit"
                                :multiple="false"
                                popper-class="max-index"
                                :params="{ 'dictionaryType': 'transportType' }">
                        </sc-select-multiple>
                        <label>物品类型：</label>
                        <sc-select-multiple
                                v-model="goodsTypeEdit"
                                :multiple="false"
                                popper-class="max-index"
                                :params="{ 'dictionaryType': 'goodsType' }">
                        </sc-select-multiple>
                    </li>
                    <li>
                        <template v-if="billsTypeStatusEdit === 0">
                            <label>结算重量：</label>
                            <el-input v-model="weightEdit" />
                        </template>
                        <label>备注：</label>
                        <el-input v-model.trim="markEdit" maxlength="30"/>
                    </li>
                </ul>
            </div>
            <div style="padding-left:24px;padding-top:16px;color:red;" v-if="isLogs">
                <div v-if="billsTypeStatusEdit === 99" key="remark">
                    <p style="font-weight: bold;">下级网点智能获取结算网点流程：</p>
                    1.面单发放网点为本网点的下级网点；2.收件或发件网点为本网点的下级网点；3.中心到件上一站为本网点的下级网点； <br />
                    <span style="font-weight: bold;">注：</span>智能获取失败或账单内有此单号，补单号失败。
                </div>
                <div v-else key="remark">
                    <p style="font-weight: bold;">自营账单补单号要求：</p>
                    1.面单发放网点为本网点；2.收件或发件网点为本网点；<br />3.中心到件上一站为本网点；4.账单无此单号；
                </div>
            </div>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="handleClose">取 消</el-button>
            <el-button @click="checkBillPost" type="primary" >确 定</el-button>
        </div>
    </el-dialog>
</template>
<script>
import { billUtils } from "@zto_module/shenzhou-components-static";
import { cloneDeep, trim } from "lodash";
export default {
    props: {
        width: {
            type: [Number],
            default: 780
        },
        editTitle: {
            type: [String],
            default: "补单号"
        },
        isBills: {
           type: Boolean,
            default: true
        },
        isLogs: {
           type: Boolean,
            default: true
        },
        checkedRows:{
            type: Array,
            default: () => {
            return []
            }
        },
        checkKey: {
            type: [String],
            default: ""
        },
    },
    data(){
        return{
            dialogVisible:false,
            billCodeEdit:"",
            billsTypeStatusEdit:0,
            billsTypeStatusOptionsEdit:[
                { value: 0, label: "自营账单" },
                { value: 99, label: "下级账单" },
            ],
            isCheckedn:true,
            isCheckedz:true,
            siteIdEdit:"",
            billTimeEdit:this.moment().format("YYYY-MM-DD"),
            userIdEdit:"",
            customerIdEdit:"",
            cityIdEdit:"",
            weightEdit:0,
            goodsTypeEdit: 1,
            goodsTypeOptions: [
                { value: 1, label: "物品" },
                { value: 2, label: "文件" }
            ],
            transportEdit:1,
            transportOptions: [
                {value: 1, label: "汽运"},
                {value: 2, label: "航空"},
                {value: 3, label: "早航班"},
                {value: 4, label: "晚航班"},
                {value: 5, label: "铁运"}
            ],
            markEdit:"",
            currRow:[],
            provId:""
        }
    },
    methods:{
        cityChange(val){
            this.provId = val.parentId;
        },
        msg(val, type = 'warning') {
            this.$message({
                showClose: true,
                message: val,
                type: type,
                duration: 2000,
            });
        },
        keyup(){
            this.billCodeEdit = this.billCodeEdit.replace(/,/g, "").replace(/，/g, "");
        },
        clearCode(){
            this.billCodeEdit = "";
        },
        handleOpen(){
            this.billCodeEdit = "";
            this.billsTypeStatusEdit = 0;
            this.isCheckedn = true;
            this.isCheckedz = true;
            this.siteIdEdit = "";
            this.billTimeEdit = this.moment().format("YYYY-MM-DD");
            this.userIdEdit = "";
            this.customerIdEdit = "";
            this.cityIdEdit = "";
            this.weightEdit = 0;
            this.goodsTypeEdit = 1;
            this.transportEdit = 1;
            this.markEdit = "";
            this.currRow = [];
            this.provId = "";
            this.dialogVisible = true;
        },
        handleClose(){
            this.dialogVisible = false;
        },
        checkBillPost(){
            let bills = [];
            if(this.isBills){
                if(!this.billCodeEdit || !trim(this.billCodeEdit) || trim(this.billCodeEdit) === ','){
                    this.msg("请输入单号");
                    return;
                }
                bills = trim(this.billCodeEdit).split(/[,\r\n]/);
                let reg = /^[a-z0-9\,]+$/i;
                if(!reg.test(bills.join(','))){
                    this.msg("当前单号有特殊字符、中文或表情");
                    return;
                }
                for (var i = 0; i < bills.length; i++) {
                    if (
                    bills[i] == "" ||
                    bills[i] == null ||
                    typeof bills[i] == undefined
                    ) {
                    bills.splice(i, 1);
                    i = i - 1;
                    }
                }
                if (bills.length > 100) {
                    this.msg('最多支持100个单号查询');
                    return;
                }
            }else{
                if(this.checkedRows.length > 0){
                    if(this.checkKey){
                        this.checkedRows.map(item =>{
                            bills.push(item[this.checkKey]);
                        });
                    }
                }else{
                    this.msg("请选择单号");
                    return;
                }
            }
            if(!this.isCheckedn){
                if(this.billsTypeStatusEdit === 99 && !this.siteIdEdit){
                    this.msg("请选择下级网点");
                    return;
                }
            }
            let options = {
                type: this.billsTypeStatusEdit,
                billDate:this.moment(this.billTimeEdit).format("YYYY-MM-DD"),
                classType:this.transportEdit,
                goodsType:this.goodsTypeEdit,
                accountWeight:this.weightEdit
            };
            if(this.billsTypeStatusEdit === 99){
                if(this.isCheckedn){
                    options.autoAccount = true;//下级网点
                }else{
                    options.oneSiteId = this.siteIdEdit;//下级网点
                }
            }else{
                if(this.isCheckedz){
                    options.autoAccount = true;
                }else{
                    if(this.customerIdEdit){
                        options.customerId = this.customerIdEdit;
                    }
                    if(this.userIdEdit){
                        options.userId = this.userIdEdit;
                    }
                }
                
            }
            if(this.cityIdEdit){
            options.cityId = this.cityIdEdit;
            options.provId = this.provId;
            }
            if(this.markEdit){
              options.remark = this.markEdit;
            }
            this.currRow = bills.map(item => {
                return {
                    ...options,
                    billCode : item
                }
            });
            this.dialogVisible = false;
            this.$emit('callBack', this.currRow);
        },
        proving(val) {
        val = val+"";
        val = val.replace(/[^\.\d\n]/g, "");
        val = val.replace(/[^\d.]/g,"");
        val = val.replace(/^\./g, "");
        val = val.replace(/\.{2,}/g,".");
        val = val.replace(".","$#$").replace(/\./g,"").replace("$#$",".");
        val = val.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数
        if(val.indexOf(".") < 0 && val !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
            val= parseFloat(val);
        }
        return val;
        }
    },
    watch: {
        isCheckedn:{
            handler(val,oldVal){
                if(val){
                    this.siteIdEdit  = "";
                }
            }
        },
        isCheckedz:{
            handler(val,oldVal){
                if(val){
                    this.userIdEdit = "";
                    this.customerIdEdit = "";
                }
            }
        },
        weightEdit:{
            handler(val, oldVal) {
                if(!val){
                this.weightEdit = 0;
                return;
                }
                let str = val+"";
                if(str.length <= 12){
                str = str.split('.');
                if(str[0].length <= 9){
                    this.weightEdit = this.proving(val);
                }else{
                    this.weightEdit = oldVal;
                }
                }else{
                this.weightEdit = oldVal;
                }
            }
        },
        markEdit:{
            handler(val, oldVal) {
                if(billUtils.regRemark(val)){
                this.markEdit = val;
                }else{
                this.markEdit = oldVal;
                }
            }
        },
        billCodeEdit:{
            handler(val, oldVal) {
                if(billUtils.regRemark(val)){
                this.billCodeEdit = val;
                }else{
                this.billCodeEdit = oldVal;
                }
            }
        }
    },
}
</script>
