const state = {
  clientHeight: 0
};
const getters = {
  clientHeight(state) {
    return state.clientHeight
  },
  waybill(state){
    return state.waybill
  }
};
const mutations = {
  setClientHeight(commit, height) {
    state.clientHeight = height;
  },
  setWaybill(state,waybill){
    state.waybill=waybill;
  }
};
export default {
  state,
  getters,
  mutations
};
