<template>
  <div class="sz-date-picker">
    <el-date-picker
      ref="start"
      v-model="myValue[0]"
      :readonly="readonly"
      :disabled="disabled"
      :size="size"
      :editable="editable"
      :format="format"
      :type="type==='daterange'?'date':'datetime'"
      :value-format="valueFormat"
      :default-time="defaultTime[0]"
      :picker-options="pickerOptionsStart"
      :placeholder="startPlaceholder"
      :clearable="clearable"
      @change="endFocusAction"
    >
    </el-date-picker>
    <div class="range-separator">{{rangeSeparator}}</div>
    <el-date-picker
      ref="end"
      v-model="myValue[1]"
      :readonly="readonly"
      :disabled="disabled"
      :size="size"
      :editable="editable"
      :format="format"
      :type="type==='daterange'?'date':'datetime'"
      :value-format="valueFormat"
      :clearable="clearable"
      :default-time="defaultTime[1]"
      :picker-options="pickerOptionsEnd"
      :placeholder="endPlaceholder"
    >
    </el-date-picker>
  </div>
</template>

<script>
// 具体使用请看文档 http://ued.dev.ztosys.com/build/zui/#/datePicker
