  name: "szDatePicker",
  props: {
    value: {
      type: Array,
      default: function() {
        return ["", ""];
      }
    },
    readonly: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    editable: {
      type: Boolean,
      default: true
    },
    clean: {
      type: [String, Number],
      default: ""
    },
    clearable: {
      type: Boolean,
      default: true
    },
    size: {
      type: String,
      default: "small"
    },
    type: {
      type: String,
      default: "daterange"
    },
    limit: {
      type: Number,
      default: 0
    },
    format: String,
    rangeSeparator: {
      type: String,
      default: ""
    },
    startPlaceholder: {
      type: String,
      default: ""
    },
    endPlaceholder: {
      type: String,
      default: ""
    },
    defaultTime: {
      type: Array,
      default: function() {
        return ["00:00:00", "23:59:59"];
      }
    },
    valueFormat: String,
    endFocus: {
      type: Boolean,
      default: true
    },
    isCheckMsg: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
      myValue: this.value,
      pickerOptionsStart: {
        disabledDate: time => {
          // if (this.myValue[1]) {
          //   var end = this.curTime(this.myValue[1]);
          //   if (this.type === "daterange") {
          //     // 日期类型
          //     if (this.limit) {
          //       // 限制天数
          //       return (
          //         time.getTime() > end ||
          //         time.getTime() + this.limit * 24 * 60 * 60 * 1000 <= end
          //       );
          //     } else {
          //       return time.getTime() > end;
          //     }
          //   } else {
          //     // 日期时间类型
          //     if (this.limit) {
          //       // 限制天数
          //       return (
          //         time.getTime() > end ||
          //         time.getTime() + (this.limit + 1) * 24 * 60 * 60 * 1000 <= end
          //       );
          //     } else {
          //       return time.getTime() > end;
          //     }
          //   }
          // }
          // return false;
        }
      },
      pickerOptionsEnd: {
        disabledDate: time => {
          // if (this.myValue[0]) {
          //   var start = this.curTime(this.myValue[0]);
          //   if (this.type === "daterange") {
          //     // 日期类型
          //     if (this.limit) {
          //       // 限制天数
          //       return (
          //         time.getTime() < start ||
          //         time.getTime() >= start + this.limit * 24 * 60 * 60 * 1000
          //       );
          //     } else {
          //       return time.getTime() < start;
          //     }
          //   } else {
          //     // 日期时间类型
          //     if (this.limit) {
          //       // 限制天数
          //       return (
          //         time.getTime() + 24 * 60 * 60 * 1000 <= start ||
          //         time.getTime() - this.limit * 24 * 60 * 60 * 1000 >= start
          //       );
          //     } else {
          //       return time.getTime() + 24 * 60 * 60 * 1000 <= start;
          //     }
          //   }
          // }
          // return false;
        }
      }
    };
  },
  watch: {
    value(val, oldVal) {
      if (val) {
        this.myValue = val;
      }
    },
    myValue() {
      this.check();
    },
    clean() {
      this.myValue[0] = "";
      this.myValue[1] = "";
      this.$refs.start.handleFieldReset();
      this.$refs.end.handleFieldReset();
      this.myValue = this.value;
    }
  },
  methods: {
    // check:_.debounce(function(key){
    //   this.check2();
    // },500),
    check() {
      // if (this.type === "datetimerange" && this.myValue[0] && this.myValue[1]) {
      //   var start = this.valueFormat
      //     ? new Date(this.myValue[0]).getTime()
      //     : this.myValue[0].getTime();
      //   var end = this.valueFormat
      //     ? new Date(this.myValue[1]).getTime()
      //     : this.myValue[1].getTime();
      //   if (start > end) {
      //     this.msg("开始时间不能大于结束时间,请重新选择时间");
      //   }
      //   if (this.limit && start + this.limit * 24 * 60 * 60 * 1000 < end) {
      //     this.myValue[1] = this.valueFormat
      //       ? this.$refs.end.formatToValue(
      //           new Date(start + this.limit * 24 * 60 * 60 * 1000)
      //         )
      //       : new Date(start + this.limit * 24 * 60 * 60 * 1000);
      //   }
      // } else if (
      //   this.type === "daterange" &&
      if (this.myValue[0] && this.myValue[1]) {
        let diff = this.moment(this.myValue[1]).diff(
          this.moment(this.myValue[0]),
          "second"
        );
        diff = diff / 86400;
        if(this.isCheckMsg){
          if (diff < 0) {
            this.msg("开始时间不能大于结束时间,请重新选择时间");
          } else {
            if (this.limit <= diff) {
              this.msg("最多可查询" + this.limit + "天的数据,请重新选择时间");
            }
          }
        }
        this.value[2] = diff;
      } else {
        if (this.value.length === 3) {
          this.value.splice(2, 1);
        }
      }
    },
    msg(val) {
      this.$message({
        showClose: true,
        message: val,
        type: "warning",
        duration: 3000
      });
    },
    curTime(value) {
      var time;
      if (this.valueFormat) {
        var dateArr = this.valueFormat.split(" ", 2);
        time =
          dateArr.length === 1
            ? new Date(value + " 00:00:00").getTime()
            : new Date(value).getTime();
      } else {
        time = value.getTime();
      }
      return time;
    },
    endFocusAction() {
      if (this.endFocus && this.myValue[0]) this.$refs.end.focus();
    }
  },
  mounted() {
    this.check();
  }
};
</script>
