import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'
Vue.use(Router);
import {
  zParam
} from '../utils/vueTool'

const router = new Router({
  routes,
  // mode: 'history',
})
// 百度统计代码
function baiduTrackPageview(to) {
  window._hmt.push(['_setAutoPageview', false]);
  let hashStr = to.path;
  let strName = to.meta.title ? to.meta.title : '';
  let joiner = hashStr.indexOf('?') > -1 ? '&' : '?';
  let str = `${window.location.pathname}#${hashStr}${joiner}systemName=${
    document.title
  }-${strName}`;
  let menuStr =
    '发件异常单处理,发件手工单处理,收中转费/客户运费,客户运费汇总,收派件代收到付,付业务员承包区派费,付发件代收到付,业务员罚款分摊,业务员预付款,预付款流水报表,固定费用项维护录入,网点小金库（日记账）';
  //   如果是不需要的菜单，就不需要埋点
  if (menuStr.indexOf(strName) < 0 || strName == '') {
    return;
  }
  if (str) {
    if (window._hmt) window._hmt.push(['_trackPageview', str]);
  }
}

router.beforeEach((to, from, next) => {
  if (to.path && self!=top) {
    if(to.fullPath.indexOf("?H") > -1){
      window.parent.postMessage({Path: to.path,sys:'bills',isGuide:'Y'}, window.location.origin);
    }else{
      window.parent.postMessage({Path: to.path,sys:'bills'}, window.location.origin);
    }
  }

  baiduTrackPageview(to )
  // 去首页
  if (to.path == '/null') {
    next(true)
    return
  }
  // 如果是没有的路由，则去首页
  if (!to.matched.length) {
    next({
      path: '/',
    })
    return
  }
  // 解决路由权限垂直越权
  function refreshUserMessage() {
    let resDt = zParam
    if ((to.path === '/' && zParam.permissions['menu_guide']) || (to.path === '/' && zParam.admin)) {
        next('/guide')
      return
    } else if (resDt.permissions && JSON.stringify(resDt.permissions) != '{}' && !resDt.admin) {
      let str = to.path
      str = str.substr(1)
      if (to.path == '/') {
        next(true)
        return
      } else if (!resDt.permissions['menu_' + str]) {
        if(!(to.path && self!=top)) {
          next('/')
        }
        return
      } else {
        next(true)
      }
    } else if (resDt.admin) {
      next(true)
    } else {
      setTimeout(() => {
        // 定时刷新
        refreshUserMessage()
      }, 1000);
    }
  }
  refreshUserMessage()
  // next(true)
})

export default router
