<template>
  <div class="spread-contianer">
    <div v-show="spreadLoading" class="spread-loading">
      <!-- <div class="el-loading-spinner">
        <svg viewBox="25 25 50 50" class="circular">
          <circle cx="50" cy="50" r="20" fill="none" class="path"></circle>
        </svg> -->
        <!-- <i class="el-icon-loading" style="fontSize:30px;"></i> -->
      <!-- </div> -->
    </div>
    <div class="spread-mod" ref="spread"></div>
    <div class="heji font-red" ref="spreadHeji" v-html="hjHtml"></div>
  </div>
</template>

<script>
// isToolTip: 6, 是否显示气泡 ,后面的是多少个字
// 合计  加showTotal
// 某一行需要弄颜色,接收一个对象
// columnsStyle: function(row) {
//           return {
//             col: '4',列的下标
//             fontColor: 'blue',字体颜色
//           }
//         },

// 全选  事件 checkboxAllClick  配置： allCheck: true,  cellType: new GC.Spread.Sheets.CellTypes.CheckBox(),
//  百分比排序 fmtPercent: [6, 9, 12], 接收的是一个数组 return 的时候不需要加百分比
// 是否显示按钮 isButton， 按钮文字，value   暂时支持单个按钮
// 按钮事件  buttonclick
// columnTip 头部提示 使用columnTip:'xxxxxxxxxxxxxxxxxxxxxx'
// headData{ isRadio:true} 控制单选

// isFrozen 冻结列

export default {
  props: {
    ngData: Array,
    headData: Object,
  },
  data() {
    return {
      spreadLoading: false,
      values: {},
      newColumns: [],
      newData: [],
      spread: null,
      sheet: null,
      hjHtml: '',
      oldArgs: {}, // 记录单选的,
      isallcheck:false
    };
  },
  created() {
    this.formatterPerc = {};
  },
  methods: {
    validateColumns(columns) {
      var hasError = false;
      if (columns && columns.length > 0) {
        var names = {};
        columns.forEach(function(item) {
          if (item.name) {
            if (names[item.name]) {
              console.error(
                '字段[' +
                  item.displayName +
                  ']和[' +
                  names[item.name] +
                  ']的name[' +
                  item.name +
                  ']重复啦'
              );
              hasError = true;
            }
            names[item.name] = item.displayName;
          } else {
            console.error('字段[' + item.displayName + ']没有设置name属性');
            hasError = true;
          }
        });
      }
      return !hasError;
    },
    transformColumn(columns) {
      let _this = this;
      _this.values = {};
      var newColumns = [];
      if (columns && columns.length > 0) {
        columns.forEach(function(column) {
          if (!column['visible'] && column['visible'] == false) {
            return;
          }
          var newColumn = {};
          for (var key in column) {
            if (key != 'value') {
              newColumn[key] = column[key];
            }
          }
          if (
            column.formatter &&
            (column.formatter == 'yyyy-MM-dd hh:mm:ss' ||
              column.formatter == 'yyyy/MM/dd hh:mm:ss' ||
              column.formatter == 'yyyy-MM-dd' ||
              column.formatter == 'yyyy/MM/dd')
          ) {
            if (!column['value']) {
              _this.values[column['name']] = function(row) {
                if (row[column.name]) {
                  if (row[column.name] instanceof Date) return row[column.name];
                  if (typeof row[column.name] == 'number')
                    return new Date(row[column.name]);
                  if (
                    typeof row[column.name] == 'string' &&
                    /[1-9][0-9]*/.test(row[column.name])
                  )
                    return new Date(parseInt(row[column.name]));
                }
              };
            }
          }
          // 如果有value回调计算的，先把函数取出
          if (column['value'] && typeof column['value'] == 'function') {
            _this.values[column['name']] = column['value'];
          }
          // 如果是checkbox则设置鼠标事件
          if (
            column.cellType &&
            column.cellType instanceof GC.Spread.Sheets.CellTypes.CheckBox
          ) {
          }
          // if (!column.formatter && !column.isCode && !(column.name.indexOf('编码') >= 0)) {
          //     column.formatter = '0';
          // }
          newColumns.push(newColumn);
        });
      }
      // 添加一个索引列，放在最后，用于行事件获取当前行的对象
      var indexColumn = {};
      indexColumn['displayName'] = '_表格索引列';
      indexColumn['name'] = '_spread_index';
      indexColumn['visible'] = false;
      newColumns.push(indexColumn);
      this.newColumns = newColumns;
      return newColumns;
    },

    setRowStyle(sheet) {
      for (var i = 0; i < sheet.getRowCount(); i++) {
        var row = sheet.getDataItem(i);
        var index = row['_spread_index'];
        var sourceRowData = this.ngData[index];
        var firstRowStyle = new GC.Spread.Sheets.Style();
        var rowStyle = this.headData.rowStyle;
        var columnsStyle = this.headData.columnsStyle;

        // 按钮
        let columns = this.headData.columns;
        var cellType2 = new GC.Spread.Sheets.CellTypes.Button();
        if (columns) {
          columns.forEach((item, index) => {
            if (item.isButton) {
              // 防止最后一个被渲染上
              if (sheet.getRowCount() - 1 == i) return;
              cellType2.text(item.value);
              sheet.getCell(i, index).cellType(cellType2);
            }
          });
        }
        // if (this.headData.columns && this.headData.columns.isButton) {
        // }
        //  百分比排序
        let newArr = this.headData.fmtPercent;
        if (this.headData.fmtPercent && this.headData.fmtPercent.length > 0) {
          // firstRowStyle.backColor = "#dce8f6";
          newArr.forEach((item, index) => {
            //如果页面数据不做X100处理 可以直接用formatter
            //sheet.getCell(i, item).formatter('0.00%')
            sheet.getCell(i, item).formatter(this.formatterPerc);
            // sheet.getCell(i, item).textDecoration(GC.Spread.Sheets.TextDecorationType.underline);
            // sheet.setStyle(i, item, firstRowStyle, GC.Spread.Sheets.SheetArea.viewport);
            // sheet.getCell(i, item).borderTop(new GC.Spread.Sheets.LineBorder("#9eb6ce", GC.Spread.Sheets.LineStyle.double));
            // sheet.getCell(i, item).borderRight(new GC.Spread.Sheets.LineBorder("#9eb6ce", GC.Spread.Sheets.LineStyle.double));
            // sheet.getCell(i, item).borderBottom(new GC.Spread.Sheets.LineBorder("#9eb6ce", GC.Spread.Sheets.LineStyle.double));
            // sheet.getCell(i, item).borderLeft(new GC.Spread.Sheets.LineBorder("#9eb6ce", GC.Spread.Sheets.LineStyle.double));
            // var h = new GC.Spread.Sheets.CellTypes.HyperLink();
            // sheet.setCellType(i, item, h, GC.Spread.Sheets.SheetArea.viewport);
          });
        } else {
          sheet.setStyle(
            i,
            -1,
            firstRowStyle,
            GC.Spread.Sheets.SheetArea.viewport
          );
        }

        if (columnsStyle) {
          if (typeof columnsStyle == 'function') {
            // 设置单元格字体颜色
            var style = new GC.Spread.Sheets.Style();
            style.foreColor = columnsStyle().fontColor;

            sheet.setStyle(
              -1,
              columnsStyle().col,
              style,
              GC.Spread.Sheets.SheetArea.viewport
            );
          }
        }
        if (typeof rowStyle == 'function' && sourceRowData) {
          var style = rowStyle(sourceRowData);
          if (style && typeof style == 'object') {
            for (var k in style) {
              firstRowStyle[k] = style[k];
            }
          }
        }

        let arr = this.headData.bkArray;
        if (this.headData.bkArray && this.headData.bkArray.length > 0) {
          firstRowStyle.backColor = '#dce8f6';
          arr.forEach((item, index) => {
            // console.log(item, i, "ss");
            sheet.setStyle(
              i,
              item,
              firstRowStyle,
              GC.Spread.Sheets.SheetArea.viewport
            );
            sheet
              .getCell(i, item)
              .borderTop(
                new GC.Spread.Sheets.LineBorder(
                  '#9eb6ce',
                  GC.Spread.Sheets.LineStyle.double
                )
              );
            sheet
              .getCell(i, item)
              .borderRight(
                new GC.Spread.Sheets.LineBorder(
                  '#9eb6ce',
                  GC.Spread.Sheets.LineStyle.double
                )
              );
            sheet
              .getCell(i, item)
              .borderBottom(
                new GC.Spread.Sheets.LineBorder(
                  '#9eb6ce',
                  GC.Spread.Sheets.LineStyle.double
                )
              );
            sheet
              .getCell(i, item)
              .borderLeft(
                new GC.Spread.Sheets.LineBorder(
                  '#9eb6ce',
                  GC.Spread.Sheets.LineStyle.double
                )
              );
          });
        } else {
          sheet.setStyle(
            i,
            -1,
            firstRowStyle,
            GC.Spread.Sheets.SheetArea.viewport
          );
        }
      }
    },
    // 设置气泡框
    setToolTip(sheet, columns) {
      for (let y = 0; y < columns.length; y++) {
        if (columns[y].isToolTip != undefined) {
          let fieldName = columns[y].name;
          for (var i = 0; i < sheet.getRowCount(); i++) {
            var row = sheet.getDataItem(i);
            var index = row['_spread_index'];
            var cellType = new GC.Spread.Sheets.CellTypes.HyperLink();
            cellType.visitedLinkColor('#666');
            cellType.linkColor('#666');
            let x = row[fieldName];
            if (x != null && x.length > columns[y].isToolTip) {
              x = x.slice(0, columns[y].isToolTip) + '...';
            } else {
              x = x;
            }
            cellType.text(x);
            cellType.linkToolTip(row[fieldName]);
            sheet
              .getCell(index, y)
              .cellType(cellType)
              .value('')
              .textDecoration(GC.Spread.Sheets.TextDecorationType.none);
            setTimeout(() => {
              sheet
                .getCell(index, columns[y].isToolTipNumber)
                .textDecoration(GC.Spread.Sheets.TextDecorationType.none);
            });
          }
          break;
        }
      }
    },
    setRowFilter(sheet) {
      //表格设置排序，筛选
      let _this = this;
      var rowCount, colCount;
      rowCount = sheet.getRowCount();
      colCount = sheet.getColumnCount();
      sheet.rowFilter(
        new GC.Spread.Sheets.Filter.HideRowFilter(
          new GC.Spread.Sheets.Range(0, 0, rowCount - 1, colCount)
        )
      );
      var filter = sheet.rowFilter();
      _this.newColumns.forEach(function(item, index) {
        if (
          !_this.newColumns[index].isHideFilter &&
          _this.newColumns[index].isHideFilter == false
        ) {
          filter.filterButtonVisible(index, false);
        }
      });
    },
    frozenCell(sheet) {
      //冻结列
      let _this = this;
      if (_this.ngData && _this.ngData.length > 0) {
        for (var i = 0; i < _this.newColumns.length; i++) {
          if (
            _this.newColumns[i].isFrozen &&
            _this.newColumns[i].isFrozen == true
          ) {
            sheet.frozenColumnCount(i + 1);
            sheet.options.frozenlineColor = 'transparent';
            break;
          }
        }
      }
    },
    setEdit(sheet) {
      //设置列可以编辑
      let _this = this;
      _this.newColumns.forEach(function(item, index) {
        if (
          _this.newColumns[index].isEdit &&
          _this.newColumns[index].isEdit == true
        ) {
          sheet.getRange(-1, index, -1, 1).locked(false);
        }
      });
    },
    sleep(numberMillis) {
      var now = new Date();
      var exitTime = now.getTime() + numberMillis;
      while (true) {
        now = new Date();
        if (now.getTime() > exitTime) return;
      }
    },
    initFormatter() {
      this.formatterPerc.prototype = GC.Spread.Formatter.FormatterBase;
      this.formatterPerc.format = function(obj, conditionalForeColor) {
        if (!isNaN(obj)) {
          return parseFloat(obj).toFixed(2) + '%';
        } else {
          return '';
        }
      };
      // 由于页面已做过处理
      // this.formatterPerc.parse = function(str) {
      //   if (!str) {
      //     return ''
      //   }
      //   return str
      // }
    },
    initSpread(spread) {
      //初始化表格参数
      // give sheet name more space
      spread.options.setTabStripRatio = 0.8;
      spread.options.scrollbarMaxAlign = true;
      spread.options.scrollbarShowMax = true;
      spread.options.grayAreaBackColor = '#e9eef2';
      spread.options.showVerticalScrollbar = true;
      spread.options.newTabVisible = false;
      spread.options.tabStripVisible = false;
      GC.Spread.Common.CultureManager.culture('zh-cn');
    },
    resetDefaultStyle(sheet) {
      //重新定义表格默认样式
      // set default style for all cells
      sheet.defaults.rowHeight = 26;
      sheet.defaults.colHeaderRowHeight = 30;
      sheet.defaults.colWidth = 100;
      sheet.options.protectionOptions = {
        allowFilter: true,
        allowSort: true,
        allowResizeRows: true,
        allowResizeColumns: true,
        allowEditObjects: true,
      };
      sheet.options.isProtected = true;
      var defaultStyle = sheet.getDefaultStyle();
      // defaultStyle.backColor = "#ffffff";
      defaultStyle.foreColor = '#666666';
      defaultStyle.font =
        '9pt "Helvetica Neue", Helvetica, Microsoft Yahei, Hiragino Sans GB, WenQuanYi Micro Hei, sans-serif';
      defaultStyle.vAlign = GC.Spread.Sheets.VerticalAlign.center;
      defaultStyle.hAlign = GC.Spread.Sheets.HorizontalAlign.center;
      sheet.setDefaultStyle(defaultStyle, GC.Spread.Sheets.SheetArea.viewport);
      sheet.setDefaultStyle(defaultStyle, GC.Spread.Sheets.SheetArea.colHeader);
      sheet.setDefaultStyle(defaultStyle, GC.Spread.Sheets.SheetArea.colHeader);
      sheet
        .getRange(0, -1, 1, -1, GC.Spread.Sheets.SheetArea.colHeader)
        .backColor('#dce8f6');
    },
    createOrUpdateSpread(data, columns) {
      let _this = this;
      _this.isallcheck = false;
      data = data ? data : [];
      let spread = _this.spread;
      let sheet = _this.sheet;
      // 校验表格列设置
      if (!_this.validateColumns(columns)) {
        return;
      }
      if (spread && sheet) {
        spread.suspendPaint();
        sheet.autoGenerateColumns = true;
        sheet.setDataSource(data);
        sheet.bindColumns(columns);
        sheet.frozenTrailingRowCount(1);
        _this.frozenCell(sheet);
        _this.setRowFilter(sheet);
        _this.setRowFilter(sheet);
        _this.setRowStyle(sheet);
        _this.setToolTip(sheet, columns);
        _this.setEdit(sheet);
        spread.resumePaint();
      } else {
        // 创建表格
        _this.spread = new GC.Spread.Sheets.Workbook(_this.$refs.spread, {});
        spread = _this.spread;
        spread.suspendPaint();
        _this.initSpread(spread);
        _this.sheet = spread.sheets[0];
        sheet = _this.sheet;
        sheet.setDataSource(data);
        sheet.bindColumns(columns);
        _this.resetDefaultStyle(sheet);
        _this.setRowFilter(sheet);
        _this.setRowStyle(sheet);
        _this.setToolTip(sheet, columns);
        _this.setEdit(sheet);
        _this.initFormatter();
        spread.resumePaint();
        console.log('asdfffffffff');
        //双击事件
        sheet.bind(GC.Spread.Sheets.Events.CellDoubleClick, function(
          sender,
          args
        ) {
          if (args.col == undefined || args.row == undefined) return;
          var col = _this.newColumns[args.col];
          var row = sheet.getDataItem(args.row);
          var index = row['_spread_index'];
          // 如果序号大于data的大小，则是汇总
          if (_this.ngData && index < _this.ngData.length) {
            var sourceRowData = _this.ngData[index];
            if (args.sheetArea == GC.Spread.Sheets.SheetArea.viewport) {
              // 点击的是数据表格区域，主体区域
              var value = sheet.getCell(args.row, args.col).text();
              if (args.col == 0 && args.sheetArea === 3) {
                if (_this.headData.columns[0].isBillCode) {
                  _this.ngData[index].check = !row.check;
                  sourceRowData = _this.ngData[index];
                }
              }
              if (col && typeof col.dblclick == 'function') {
                col.dblclick(value, sourceRowData);
              } else if (typeof _this.headData.dblclick == 'function') {
                _this.headData.dblclick(value, sourceRowData, args);
              }
            } else if (args.sheetArea == GC.Spread.Sheets.SheetArea.rowHeader) {
              // 点击的是行号，整行
              if (typeof _this.headData.dblclick == 'function') {
                var value = sheet.getCell(args.row, args.col).text();
                _this.headData.dblclick(value, sourceRowData, args);
              }
            }
          }
        });
        var setValueChecked = function(row, checked) {
          var row = sheet.getDataItem(row);
          var col = _this.newColumns[0];
          var index = row['_spread_index'];
          var sourceRowData = _this.ngData[index];
          if (sourceRowData) sourceRowData[col.name] = checked;
        };
        // 单击事件
        sheet.bind(GC.Spread.Sheets.Events.CellClick, function(sender, args) {
          if (args.col == 0 && args.row == 0) {
            if (!_this.headData.columns[0].allCheck) {
              return;
            }
            if (args.sheetArea === 1) {
              var cell = sheet.getCell(0, 0);
              _this.$emit('checkboxAllClick', cell.value());
            }
          }
          try {
            // document.onmousedown();
          } catch (e) {
            console.error(e);
          }
          var filter = sheet.rowFilter();
          if (args.col == undefined || args.row == undefined) return;
          if (
            args.col == 0 &&
            args.row == 0 &&
            args.sheetArea == GC.Spread.Sheets.SheetArea.colHeader
          ) {
            _this.spreadLoading = true;
            // _this.sleep(1000);
            setTimeout(function() {
              // _this.$store.commit('setLoading', true);
              // if (sheet.getValue(0, 0) == true) {
                if(_this.isallcheck){
                spread.suspendPaint();
                for (var i = 0; i < sheet.getRowCount(); i++) {
                  let cell = sheet.getCell(i, 0);
                  let cellType = sheet.getCellType(i, 0);
                  if (cellType instanceof GC.Spread.Sheets.CellTypes.CheckBox) {
                    // 如果是取消，则全部都取消，不管在不在筛选结果中
                    setValueChecked(i, false);
                    cell.value(false);
                    setValueChecked(i, false);
                    cell.value(false);
                    if (i === sheet.getRowCount() - 1) {
                      _this.$emit('checkboxAllClick', _this.ngData[i]);
                      _this.isallcheck = false;
                    }
                  }
                }
                spread.resumePaint();
              } else {
                spread.suspendPaint();
                for (var i = 0; i < sheet.getRowCount() - 1; i++) {
                  let cell = sheet.getCell(i, 0);
                  let cellType = sheet.getCellType(i, 0);
                  if (cellType instanceof GC.Spread.Sheets.CellTypes.CheckBox) {
                    // 如果是选中，则不在筛选结果中的，要取消
                    if (!filter.isRowFilteredOut(i)) {
                      setValueChecked(i, true);
                      cell.value(true);
                    } else {
                      setValueChecked(i, false);
                      cell.value(false);
                    }
                    if (i === sheet.getRowCount() - 2) {
                      _this.$emit('checkboxAllClick', _this.ngData[i]);
                      _this.isallcheck = true;
                    }
                  }
                }
                spread.resumePaint();
              }
              // _this.$store.commit('setLoading', false);
              _this.spreadLoading = false;
            }, 10);
          }
          // 点击列头后显示列汇总信息
          if (args.sheetArea == GC.Spread.Sheets.SheetArea.colHeader) {
            // 因为点击列头也会出发SelectionChanged事件，所以这里就不用写了
          }
        });
        spread.bind(GC.Spread.Sheets.Events.SelectionChanged, function(
          e,
          args
        ) {
          var html = '';
          if (
            args.newSelections.length == 1 &&
            args.newSelections[0].colCount == 1
          ) {
            // 只处理一次性选择的，不支持多区域选
            var sheet = args.sheet;
            var selection = args.newSelections[0];
            var sum = 0,
              cnt = 0;
            var hasNotText = false;
            var index = selection.row;
            var filter = sheet.rowFilter();
            index = index >= 0 ? index - 1 : -1;
            var rowCount = selection.rowCount;
            let reg = /%+/;
            let isPercentage = false;
            while (rowCount-- > 0) {
              index++;
              // 判断是否在筛选范围内
              if (
                filter.isRowFilteredOut(index) ||
                index >= sheet.getRowCount() - 1
              ) {
                continue;
              }
              cnt++;
              var cellType = sheet.getCellType(index, selection.col);
              // 此分支排除 checkbox select下拉框 等
              if (
                !hasNotText &&
                cellType instanceof GC.Spread.Sheets.CellTypes.Text
              ) {
                var val = sheet.getValue(index, selection.col);
                if (!val) val = 0;
                if (reg.test(val)) {
                  isPercentage = true;
                  val = parseFloat(val.substring(0, val.length - 1));
                }
                // val = Number(val);
                if (!_this.headData.fmtPercent) {
                  _this.headData.fmtPercent = [];
                }
                let FmtPerc = _this.headData.fmtPercent;
                let blFmtPerc =
                  FmtPerc.length > 0 && FmtPerc.indexOf(selection.col) >= 0;
                if (!isNaN(val)) {
                  // val = Number(val);
                  if (typeof val == 'number' && !blFmtPerc) {
                    sum += val;
                  } else {
                    // console.log('第' + index + '行不是数字：' + val);
                    // 不是数字，则只显示统计数量
                    hasNotText = true;
                  }
                } else {
                  hasNotText = true;
                }
              } else {
                // 不是文本类型单元格，则只显示统计数量
                hasNotText = true;
              }
            }
            if (cnt > 0) {
              html = '计数: ' + cnt + ';';
              if (!hasNotText) {
                var avg = sum / cnt;
                if (isPercentage) {
                  html =
                    html +
                    '合计: ' +
                    sum.toFixed(2) +
                    '%' +
                    ';平均: ' +
                    avg.toFixed(4) +
                    '%';
                } else {
                  html =
                    html +
                    '合计: ' +
                    sum.toFixed(2) +
                    ';平均: ' +
                    avg.toFixed(4);
                }
              }
              html = html + '<span"> (不包含汇总行) </span>';
            }
          } else {
            // 非连续性选择，只提示选择数量
            // 非同一列选择，只提示选择数量
            var cnt = 0;
            args.newSelections.forEach(function(selection) {
              cnt = cnt + selection.colCount * selection.rowCount;
            });
            html = '计数:' + cnt;
            html = html + '<span"> (不包含汇总行) </span>';
          }
          _this.hjHtml = html;
        });

        // 筛选完成事件，刷新选中区域的汇总，和修改非筛选结果的行为未选中状态
        spread.bind(GC.Spread.Sheets.Events.RangeFiltered, function(e, args) {
          var filter = sheet.rowFilter();
          spread.suspendPaint();
          // 筛选完刷新check值
          for (var i = 0; i < sheet.getRowCount(); i++) {
            var cell = sheet.getCell(i, 0);
            var cellType = sheet.getCellType(i, 0);
            if (cellType instanceof GC.Spread.Sheets.CellTypes.CheckBox) {
              // 如果是选中，则不在筛选结果中的，要取消
              if (filter.isRowFilteredOut(i)) {
                setValueChecked(i, false);
                cell.value(false);
              }
            }
          }
          // 刷新下面的汇总值，筛选后直接清除汇总结果，让用户重新选择
          _this.hjHtml = '';
          spread.resumePaint();
        });
        spread.bind(GC.Spread.Sheets.Events.RangeSorted, function(e, args) {
          // 刷新行样式
          spread.suspendPaint();
          _this.setRowStyle(sheet);
          spread.resumePaint();
          _this.hjHtml = '';
          // $(_this.$refs.spreadHeji).html("");
        });
        spread.bind(GC.Spread.Sheets.Events.ButtonClicked, function(e, args) {
          if (_this.headData.isRadio) {
            //让复选框成为单选状态
            if (args.row !== _this.oldArgs.row) {
              sheet.getCell(_this.oldArgs.row, 0).value(false);
            } else {
              //如果当前点击的和上次点击的是同一行，就始终处于勾选状态
              sheet.getCell(_this.oldArgs.row, 0).value(true);
            }
            _this.oldArgs = {...args};
          }
          var cellType = args.sheet.getCellType(args.row, args.col);
          if (cellType instanceof GC.Spread.Sheets.CellTypes.Button) {
            var getSource = args.sheet.getDataItem(args.row);
            // 根据序号找到原始记录
            var index = getSource['_spread_index'];
            if (_this.ngData && index < _this.ngData.length) {
              var sourceRowData = _this.ngData[index];
              _this.newColumns[args.col].buttonclick(sourceRowData);
            }
          } else if (cellType instanceof GC.Spread.Sheets.CellTypes.CheckBox) {
            var getSource = args.sheet.getDataItem(args.row);
            // 根据序号找到原始记录
            var index = getSource['_spread_index'];
            if (_this.ngData && index < _this.ngData.length) {
              if (_this.headData.isRadio) {
                for (var i = 0; i < sheet.getRowCount(); i++) {
                  var cell = sheet.getCell(i, 0);
                  var cellType = sheet.getCellType(i, 0);
                  if (
                    cellType instanceof GC.Spread.Sheets.CellTypes.CheckBox &&
                    i != args.row
                  ) {
                    // 如果是选中，则不在筛选结果中的，要取消
                    setValueChecked(i, false);
                  }
                }
              }

              var sourceRowData = _this.ngData[index];
              var col = _this.newColumns[0];
              var val = args.sheet.getValue(args.row, args.col);
              if (!_this.headData.isRadio) {
                if (sourceRowData.check === val) {
                  val = !val;
                }
              }

              sourceRowData[col.name] = val;
              if (col.onchange) col.onchange(sourceRowData, val);
            }
          }
        });
      }
      // 添加头部问号提示
      let spreadNS = GC.Spread.Sheets;
      for (let i = 0; i < _this.newColumns.length; i++) {
        if (_this.newColumns[i].columnTip) {
          function HighlightColumnItemsCellType() {
            this.RADIUS = 10;
            this.HIGHLIGHT_COLOR = '#666';
            this.NORMAL_COLOR = '#666';
            this.HIGHLIGHT_TIP = _this.newColumns[i].columnTip;
            this.NORMAL_TIP = _this.newColumns[i].columnTip;

            spreadNS.CellTypes.ColumnHeader.apply(this);
          }

          HighlightColumnItemsCellType.prototype = new spreadNS.CellTypes.ColumnHeader();
          HighlightColumnItemsCellType.prototype.paint = function(
            ctx,
            value,
            x,
            y,
            width,
            height,
            style,
            context
          ) {
            spreadNS.CellTypes.ColumnHeader.prototype.paint.apply(
              this,
              arguments
            );
            var tag = context.sheet.getTag(
              context.row,
              context.col,
              context.sheetArea
            );
            var RADIUS = this.RADIUS;
            ctx.save();
            ctx.beginPath();
            var img = new Image();
            img.src =
              'data:image/png;base64,';
            ctx.drawImage(
              img,
              x + width - RADIUS - 35,
              y + height / 2 - 7,
              15,
              15
            );
            // ctx.restore();
            ctx.restore();
          };
          HighlightColumnItemsCellType.prototype.getHitInfo = function(
            x,
            y,
            cellStyle,
            cellRect,
            context
          ) {
            let RADIUS = this.RADIUS;
            let centerX = cellRect.x + cellRect.width - RADIUS - 30,
              centerY = cellRect.y + cellRect.height / 2;
            let hitInfo = {
              x: x,
              y: y,
              row: context.row,
              col: context.col,
              cellRect: cellRect,
              sheetArea: context.sheetArea,
              sheet: context.sheet,
            };
            if (
              Math.abs(x - centerX) < RADIUS - 3 &&
              Math.abs(y - centerY) < RADIUS - 3
            ) {
              hitInfo.isReservedLocation = true;
            }
            return hitInfo;
          };
          HighlightColumnItemsCellType.prototype.processMouseDown = function(
            hitInfo
          ) {
            this._hideTip();
          };
          HighlightColumnItemsCellType.prototype.processMouseMove = function(
            hitInfo
          ) {
            if (hitInfo.isReservedLocation) {
              var sheet = hitInfo.sheet;
              var offset = {
                top: sheet.getParent().getHost().offsetTop,
                left: sheet.getParent().getHost().offsetLeft,
              };
              var tag = sheet.getTag(
                hitInfo.row,
                hitInfo.col,
                hitInfo.sheetArea
              );
              this._showTip(
                offset.top + hitInfo.y + 190,
                offset.left + hitInfo.x + 200,
                (tag && tag.tip) || this.NORMAL_TIP
              );
            } else {
              this._hideTip();
            }
          };
          HighlightColumnItemsCellType.prototype.processMouseUp = function(
            hitInfo
          ) {
            if (hitInfo.isReservedLocation) {
              var sheet = hitInfo.sheet;
              var tag = sheet.getTag(
                hitInfo.row,
                hitInfo.col,
                hitInfo.sheetArea
              );
              if (!tag) {
                //first time
                tag = {
                  color: this.HIGHLIGHT_COLOR,
                  tip: this.HIGHLIGHT_TIP,
                };
                sheet.setTag(hitInfo.row, hitInfo.col, tag, hitInfo.sheetArea);

                var style = new spreadNS.Style();
                style.foreColor = 'red';
                var ranges = [new spreadNS.Range(-1, hitInfo.col, -1, 1)];

                var rule = sheet.conditionalFormats.addCellValueRule(
                  spreadNS.ConditionalFormatting.ComparisonOperators.lessThan,
                  0,
                  0,
                  style,
                  ranges
                );

                tag.rule = rule;
                tag.isHighlighed = true;
              } else if (!tag.isHighlighed) {
                tag.color = this.HIGHLIGHT_COLOR;
                tag.tip = this.HIGHLIGHT_TIP;

                sheet.conditionalFormats.addRule(tag.rule);

                tag.isHighlighed = true;
              } else {
                tag.color = this.NORMAL_COLOR;
                tag.tip = this.NORMAL_TIP;

                sheet.conditionalFormats.removeRule(tag.rule);

                tag.isHighlighed = false;
              }

              var offset = {
                top: sheet.getParent().getHost().offsetTop,
                left: sheet.getParent().getHost().offsetLeft,
              };
              this._showTip(
                offset.top + hitInfo.y + 190,
                offset.left + hitInfo.x + 200,
                (tag && tag.tip) || this.NORMAL_TIP
              );
            }
          };
          HighlightColumnItemsCellType.prototype.processMouseEnter = function(
            hitInfo
          ) {};
          HighlightColumnItemsCellType.prototype.processMouseLeave = function(
            hitInfo
          ) {
            this._hideTip();
          };
          HighlightColumnItemsCellType.prototype._showTip = function(
            top,
            left,
            tip
          ) {
            if (!this._tipElement) {
              var span = document.createElement('span');
              span.style.position = 'absolute';
              span.style.background = '#EEEEEE';
              span.style.border = '1px solid black';
              span.style.boxShadow = '5px 5px 5px rgba(0,0,0,0.4)';
              span.style.fontSize = '14px';
              span.style.padding = '3px 5px';
              document.body.insertBefore(span, null);
              this._tipElement = span;
            }
            let tipElement = this._tipElement;
            tipElement.textContent = tip;
            let spanStyle = tipElement.style;
            spanStyle.top = top + 'px';
            spanStyle.left = left + 'px';
          };
          HighlightColumnItemsCellType.prototype._hideTip = function() {
            if (this._tipElement) {
              document.body.removeChild(this._tipElement);
              this._tipElement = undefined;
            }
          };

          sheet.suspendPaint();
          sheet.setCellType(
            0,
            i,
            new HighlightColumnItemsCellType(),
            spreadNS.SheetArea.colHeader
          );
          sheet.resumePaint();
        }
      }
    },
    transformData(newVal) {
      var _this = this;
      _this.newData = [];
      if (newVal && newVal.length > 0) {
        var rowCount = 0;
        var totalRow = {};
        newVal.forEach(function(item) {
          // 当前行计算完成加入新数组
          var newItem = {};
          _this.newColumns.forEach(function(column) {
            var key = column['name'];
            // 检查有没有value回调函数，如果有，则计算数值后再显示
            if (_this.values[key]) {
              newItem[key] = _this.values[key](item);
            } else {
              newItem[key] = item[key];
            }
            if (column.showTotal) {
              var val = 0;
              try {
                if (newItem[key]) val = parseFloat(newItem[key]);
              } catch (e) {}
              if (totalRow[column.name]) {
                totalRow[key] += val;
              } else {
                totalRow[key] = val;
              }
            }
          });
          newItem['_spread_index'] = rowCount++;
          _this.newData.push(newItem);
        });
        for (var key in totalRow) {
          let calculatedTotal = totalRow[key];
          const reg = /^\d+$/;
          if (!reg.test(calculatedTotal)) {
            totalRow[key] = totalRow[key].toFixed(2) / 1;
          } else {
            totalRow[key] = totalRow[key];
          }
        }
        totalRow['_spread_index'] = rowCount++;
        _this.newData.push(totalRow);
      }
    },
  },
  watch: {
    ngData: {
      handler: function(newVal, oldVal) {
        this.transformColumn(this.headData ? this.headData.columns : []);
        this.transformData(newVal);
        this.createOrUpdateSpread(this.newData, this.newColumns);
      },
      deep: true,
    },
    headData: {
      handler: function(newVal, oldVal) {
        // 先刷成空表格
        this.createOrUpdateSpread(
          [],
          [{displayName: '_表格索引列', name: '_spread_index'}]
        );
        // 再刷新数据
        this.transformColumn(newVal.columns);
        this.transformData(this.ngData);
        this.createOrUpdateSpread(this.newData, this.newColumns);
        this.hjHtml = '';
      },
      deep: true,
    },
  },
  mounted() {
    // 再刷新数据
    if (this.headData && this.headData.columns) {
      this.transformColumn(this.headData.columns);
      this.transformData(this.ngData);
      this.createOrUpdateSpread(this.newData, this.newColumns);
      this.hjHtml = '';
    }
  },
};
</script>

