<template>
  <div class="sz-settlement-component del-arrow-up">
    <el-select
      v-model="inputValue"
      :clearable="clearable"
      :filterable="true"
      :disabled="disabled"
      :filter-method="remote"
      @change="changeItem"
      @clear="clear"
      @blur="blur"
      no-match-text=" "
      :placeholder="placeholder"
      :popperClass="popperClass"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.name + item.code"
        :value="item.id"
      />
    </el-select>
  </div>
</template>

<script>
/**
 * v-model返回的是网点的id
 * model事件返回的是选中结算对象的整个列
 * 回填根据id回调值，可能暂时没有这个，所以可能回填失败
 * clearable是否可以清空
 * name * 名称或者编码 * ^code^ 编码必须是开头匹配；格式:"^code^ 苏州" * ^name^ 名称必须是开头匹配；格式:^name^ 苏州" * ^all^ 名称和编码都必须是 *开头匹配；格式:^all^ 苏州" * ^custom^ %号由用户自定义；格式:^custom^ 苏州" java.lang.String
 *parentId * 可从登陆信息中获取 * 父网点id，如果设置了child=true，则查询此参数的下级网点 java.lang.Long
 *provId * 网点所在省id java.lang.Long
 *provIds * 可以是多个省份id java.util.List[]
 *cityId * 网点所在城市id java.lang.Long
 *cityIds * 可以是多个城市id java.util.List[]
 *level * 网点的加盟级别，只支持 1:一级加盟，2：二级加盟，3：三级加盟，4：四级加盟，5：五级加盟；配合child使用，可以展示包含子网点 java.lang.Integer
 *child * 是否只查询子网点 java.lang.Boolean
 *self * 如果只查询子网点，是否要包含当前网点自己 java.lang.Boolean
 *recursion * 是否递归包含子网点的子网点，只有在按parentId查询，才有效 boolean
 *manageAreas * 管理区,使用管理区会抛弃省市条件 java.util.List[]
 *enabled * 启用状态(0禁用/1启用) java.lang.Integer
 *inSiteIds * in 网点id集合 java.util.List[]
 *noSiteIds * not in 网点id集合 java.util.List[]
 *permission 如果为true，则按照设定的用户数据权限赋权 java.lang.Boolean
 *id
 */
import { getSiteData } from "@/config/api";
import { truncate } from 'fs';
export default {
  props: {
    value: {
      type: [Number, String]
    },
    default: [Number, String],
    clearable: {
      type: Boolean,
      default: true
    },
    hasSubordinate: {
      type: Boolean,
      default: true
    },
    placeholder: {
      type: String,
      default: ""
    },
    popperClass: {
      type: String,
      default: ""//acconunt-name-class
    },
    // 接收网点接口的所有参数值
    param: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      inputValue: this.value,
      options: [],
      globalOptions: [],
      loading: false,
      disabled: false
    };
  },
  mounted() {
    this.getSettlement();
  },
  methods: {
    // 远程搜索方法
    remote: _.debounce(function(key) {
      this.getSettlement(key);
    }, 1000),
    // 获取结算对象
    getSettlement(key) {
      if(!this.hasSubordinate){
        this.disabled = true;
        this.options = [{
          "id":this.z_param.siteInfo.id,
          "name":this.z_param.siteInfo.siteName,
          "code":this.z_param.siteInfo.siteCode
        }];
        this.$emit("input", this.z_param.siteInfo.id);
      }else{
        let options = {
          name: key,
          ...this.param
        };
      getSiteData(options).then(res => {
        if (res.data && res.data.length > 0) {
          this.options = res.data;
          if (this.globalOptions.length === 0) {
            this.globalOptions = res.data;
          }
        } else {
          // this.msg("没有查询到数据");
          this.options = [];
        }
      }).catch((response) => {});
      }
    },
    // 将得到的值抛出去
    changeItem(key) {
      let tempObj = this.options.filter(function(ele) {
        return ele.id == key ? true : false;
      });
      let chooseObj = tempObj[0] ? tempObj[0] : null;
      this.$emit("input", this.inputValue);
      this.$emit("model", this.chooseObj);
    },
    chooseOne(key) {
      let tempObj = this.options.filter(function(ele) {
        return ele.id == key ? true : false;
      });
      let chooseObj = tempObj[0] ? tempObj[0] : null;
      this.z_param.arrInfo = [];
      if (chooseObj != null) {
        if (chooseObj.type == 4) {
          this.z_param.arrInfo.push(chooseObj);
        }
      }
    },
    chooseId(key,keyId, callBack) {
      if(!this.hasSubordinate){
        callBack(self, this.inputValue);
        // this.disabled = true;
        // this.options = [{
        //   "id":this.z_param.siteInfo.id,
        //   "name":this.z_param.siteInfo.siteName,
        //   "code":this.z_param.siteInfo.siteCode
        // }];
        // this.$emit("input", this.z_param.siteInfo.id);
      }else{
        let options = {
          name: key,
          ...this.param
        };
      getSiteData(options).then(res => {
        if (res.data && res.data.length > 0) {
          this.options = res.data;
          if (this.globalOptions.length === 0) {
            this.globalOptions = res.data;
          }
        } else {
          // this.msg("没有查询到数据");
          this.options = [];
        }
        this.$emit("input", keyId);
        callBack(self, this.inputValue);
      }).catch((response) => {});
      }
    },
    blur() {
      if (this.options.length === 0) {
        setTimeout(() => {
          this.options = this.globalOptions;
        }, 1000);
      }
    },
    clear() {
      this.inputValue = "";
      this.options = this.globalOptions;
      // this.getSettlement();
    },
    msg(val) {
      this.$message({
        showClose: true,
        message: val,
        type: "warning",
        duration: 5000
      });
    }
  },
  watch: {
    value(value, oldValue) {
      this.inputValue = value;
      this.$emit("input", value);
    }
  }
};
</script>

