<template>
  <!--iframe页面展示区域-->
  <div id="frameBox">
    <el-tabs :value="selectTab" class="full" type="card">
      <el-tab-pane
        v-for="item in frameTabData"
        :key="item.name"
        :label="item.name"
        :name="item.name"
      >
        <iframe
          :src="item.url"
          :key="item.name"
          :name="item.name"
          frameborder="0"
          v-if="frameViews.indexOf(item.name) > -1"
        ></iframe>
      </el-tab-pane>
    </el-tabs>
   
  </div>
</template>
<script>
import {mapState} from 'vuex';
const domainName = window.location.origin
const version = parseInt(new Date().getTime() / 60000)
export default {
  name: 'frameView',
  props: ['selectTab', 'iframeUrl'],
  computed: {
    ...mapState({
      frameViews: state => state.frame.frameViews,
    }),
  },
  data() {
    return {
      tabUrl: '',
      frameTabData: [
        {
          name: 'thrall', // 与to.meta.iframe保持一致
          domain:`${domainName}/thrall/?v=${version}/`, // 主域名
          url: `${domainName}/thrall/?v=${version}`, // 主域名拼接路径
          mode: '/#', // iframe引入项目的路由模式。hash路由 '/#'  brower路由 ''
        },
        {
          name: 'datacenter', // 与to.meta.iframe保持一致
          domain:`${domainName}/datacenter/?v=${version}/`, // 主域名
          url: `${domainName}/datacenter/?v=${version}`, // 主域名拼接路径
          mode: '/#', // iframe引入项目的路由模式。hash路由 '/#'  brower路由 ''
        },
      ],
    };
  },
  watch: {
    selectTab(val, oldVal) {
      this.tabUrl = this.selectTab + this.iframeUrl;
    },
    iframeUrl: {
      handler: function(newVal, oldVal) {
        this.tabUrl = this.selectTab + this.iframeUrl;
      },
      deep: true,
    },
    tabUrl(val, oldVal) {
      this.iframeJump();
    },
  },
  methods: {
    iframeJump() {
      console.log(this.selectTab, this.iframeUrl);
      let domain = '';
      for (var v of this.frameTabData) {
        if (this.selectTab === v.name) {
          domain = v.domain;
          if (!window.frames[this.selectTab]) {
            v.url = v.domain + v.mode + this.iframeUrl;
          }
        }
      }
      if (window.frames[this.selectTab] && this.selectTab != '') {
        window.frames[this.selectTab].postMessage(this.iframeUrl, domain);
      }
    },
  },
  created() {
    this.iframeJump();
  },
};
</script>
<style lang="scss" scope>
#frameBox {
  position: absolute;
  left: 0;
  right: 0;
  top: 0px;
  bottom: 0;
  z-index: 10 !important;
  .el-tab-pane {
    height: 100%;
    width: 100%;
    iframe {
      height: 100%;
      width: 100%;
    }
  }
  .full {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  .el-tabs__header {
    display: none;
  }
  .el-tabs__content {
    width: 100%;
    height: 100%;
  }
}
</style>
