<script>
export default {
  props: {
    msg: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      showMore: true,
    };
  },
  methods: {
    toggleShowMore () {
      if(!this.showMore){
        this.$emit("input", 0);
      }
      this.showMore = !this.showMore;
      if(!this.showMore){
        this.$emit("input", this.$refs.collapseRef.clientHeight);
      }
    }
  }
};
</script>
