<template>
  <div
    id="app"
    v-loading.fullscreen.lock="loading"
    :class="isIframe ? 'iframe' : ''"
  >
    <div v-if="isLogin" :class="isNode ? '' : 'no-pd0'">
      <!-- <my-head v-if="isNode" />
      <menu-list v-if="isNode" ref="menuList" /> -->
      <my-head v-show="isNode && !isActive" />
      <menu-list v-if="isNode && !isActive" ref="menuList" />
      <!--
        :class="{widther: collapse, 'less-width': !collapse}"
      -->
      <div
        class="page-container"
        :class="{'widther':collapse,'less-width':!collapse,'isIframe':isActive}"
        :status="collapse"
        ref="pagecontent"
        id="test"
      >
        <template v-if="!isActive">
          <keep-alive :include="keepRoutes">
            <router-view />
          </keep-alive>
        </template>
        <template v-if="isActive">
          <keep-alive :include="z_param.tabH">
            <router-view></router-view>
          </keep-alive>
        </template>
        <div class="frame-box" v-show="!viewType">
          <frame-view
            :selectTab="selectTab"
            :iframeUrl="iFrameUrl"
          ></frame-view>
        </div>
      </div>
    </div>
    <div v-if="!isLogin">
      <div class="loading-ball">
        <span class="ball-loader">Loading&#8230;</span>
      </div>
    </div>
  </div>
</template>

<script>
import myHead from './components/common/MyHead';
import menuList from './components/common/MenuList';
import {getUserData, getTour, saveTour} from './config/api';
import sourceMenuTour from './config/tour';
import './utils/intro.js';
import {mapGetters, mapActions, mapState} from 'vuex';
import frameView from './components/frameView';
import {ColorPicker} from 'element-ui';
import {cloneDeep} from 'lodash';
import {getCost} from '@/config/api';

var intro = introJs();
export default {
  name: 'app',
  components: {
    myHead,
    menuList,
    frameView,
  },
  computed: {
    ...mapGetters([
      'collapse',
      'loading',
      'menus',
      'navTabs',
      'keepRoutes',
      'isLogin',
    ]),
    ...mapState({
      viewType: state => state.frame.viewType,
    })
  },
  watch: {
    $route(to, from) {
      this.catView(to);
      // intro && window.location.reload();
      if (intro) {
        intro.exit()
        setTimeout(() => {
          intro = introJs();
          this.initTour();
          intro && intro.refresh();
        }, 1000);
      }
    },
  },
  created() {
    this.targetMenu = [];
  },
  destroyed() {
    window.removeEventListener('resize', this.reLayout);
  },
  mounted() {
    this.isActive = self != top?true:false
    this.z_param.isActiveH = this.isActive;
    this.getUserInfo();
    // window.addEventListener('resize', this.onResize);
    const strIfram = window.location.href;
    if (strIfram.indexOf('iframe') > -1) {
      this.isIframe = true;
    }
    this.$store.commit('setClientHeight', document.body.clientHeight);
    window.addEventListener('resize', this.reLayout);
    // console.log( ,'ddd')
    // 判断是生产还是测试 兼容结算那边的iframe引入
    if (process.env.NODE_ENV != 'development') {
      let str = window.location.href;
      if (str.indexOf('thrall') > -1) {
        this.isNode = false;
        this.z_param.isNode = false;
      }
    }

    getCost({}).then(res => { // 面单成本
      if (res.result == null||(res.result.comSignle == 0 && res.result.elecSignle == 0 && res.result.xlSignle == 0)) {
         return window.parent.postMessage({fromType:'hideManue',idx:6,mark:'menu_waybill'}, window.location.origin);
      }
      let data = res.result;
      // if (data.comSignle == 0 && data.elecSignle == 0 && data.xlSignle == 0) {
      //   return window.parent.postMessage({fromType:'hideManue',idx:6,mark:'menu_waybill'}, window.location.origin);
      // }
      this.$store.commit('setWaybill',data);
      console.log(this.$store.state.pub)
    })
  },
  data() {
    return {
      active: true,
      isNode: true,
      // targetMenu:[],
      isIframe: false,
      selectTab: '',
      iFrameUrl: '',
      isActive:false,
    };
  },
  methods: {
    ...mapActions([
      'setLoading',
      'setMenusNone',
      'setLimitMenus',
      'setIsLogin',
      'catViewType',
      'addFrameViews',
    ]),

    initTour() {
      let sourceMenu = this.targetMenu;
      // 暂定上线
      // intro = null;
      // return;
      //防止结算嵌入iframe
      if (self != top) {
        intro = null;
        return;
      }
      if (sourceMenu.length < 1) {
        intro = null;
        return;
      }
      getTour({}).then(res => {
        try {
          if (res.code === 0 && res.data === false) {
            // console.log(tourMenu.sort(function(s, t){
            //   return s.order -  t.order;
            // }))

            // setTimeout(() => {
            this.$nextTick(() => {
              var nextTour = [...this.targetMenu];
              let sourceKey = nextTour[0].key;
              if (sourceKey && sourceKey.indexOf('-') > -1) {
                this.$refs.menuList.defaultOpen = [sourceKey.split('-')[0]];
              } else {
                this.$refs.menuList.defaultOpen = [sourceKey];
              }

              this.$nextTick(() => {
                setTimeout(() => {
                  intro && intro.refresh();
                  if (sourceMenu[0].rowspan) {
                    // var pre = ele.getAttribute('class') || '';
                    // ele.setAttribute('class', pre + ' introjs-showElement' );
                    let clsArr = sourceMenu[0].rowspanCls;
                    clsArr.forEach(item => {
                      let ele = document.querySelector(item);
                      if (ele.classList !== undefined) {
                        ele.classList.add('introjs-showElement');
                      }
                    });

                    // if (ele.classList !== undefined) {
                    //     ele.classList.add("introjs-showElement" );
                    // }else{
                    //   ele.className = ele.className.replace("introjs-showElement", '').replace(/^\s+|\s+$/g, '');
                    // }
                  }
                }, 350);
              });

              // this._introItems[this._currentStep]

              var _this = this;

              setTimeout(() => {
                intro
                  .setOptions({
                    autoWidth: true,

                    steps: nextTour,
                  })
                  .start()
                  .onbeforechange(function(ele, step) {
                    intro.refresh();
                    let sourceKey = nextTour[step].key;

                    if (sourceKey && sourceKey.indexOf('-') > -1) {
                      _this.$refs.menuList.defaultOpen = [
                        sourceKey.split('-')[0],
                      ];
                      // _this.$store.commit("setActiveTab", {active: {key: sourceKey}});
                    } else {
                      _this.$refs.menuList.defaultOpen = [sourceKey];
                      // _this.$store.commit("setActiveTab", {active: {key: sourceKey + "-1"}});
                    }
                  })
                  .onafterchange(function(ele, step) {
                    // console.log("onafterchange")
                    intro.refresh();
                    _this.$nextTick(() => {
                      setTimeout(() => {
                        intro.refresh();
                        if (sourceMenu[step].rowspan) {
                          // var pre = ele.getAttribute('class') || '';
                          // ele.setAttribute('class', pre + ' introjs-showElement' );
                          let clsArr = sourceMenu[step].rowspanCls;
                          clsArr.forEach(item => {
                            let ele = document.querySelector(item);
                            if (ele.classList !== undefined) {
                              ele.classList.add('introjs-showElement');
                            }
                          });

                          // if (ele.classList !== undefined) {
                          //     ele.classList.add("introjs-showElement" );
                          // }else{
                          //   ele.className = ele.className.replace("introjs-showElement", '').replace(/^\s+|\s+$/g, '');
                          // }
                        }
                      }, 350);
                    });
                  })
                  .onchange(function() {})
                  .onexit(function() {
                    intro = null;
                  })
                  .oncomplete(function(val) {
                    if (val) {
                      saveTour();
                    }
                    _this.$router.push({path: '/guide'});
                    _this.$store.commit('setActiveTab', {active: {key: '1-1'}});
                  })
                  .onremind(function(val) {});
              }, 500);
            });
            // }, 500)
          } else {
            intro = null;
          }
        } catch (e) {
          intro = null;
        }
      });
    },
    autoBl(){
      console.log("autoBl");
    },
    catView(to) {
      if (to.meta.iframe && to.meta.iframeUrl) {
        this.selectTab = to.meta.iframe || '';
        this.iFrameUrl = to.meta.iframeUrl || '';
        this.catViewType(false);
        console.log(to,'klkk');
        this.addFrameViews(to);
      } else {
        console.log(to,'kkk');
        this.catViewType(true);
      }
      // if(this.isActive){
        // let dom =document.getElementsByClassName('el-select-dropdown')
        // if(!dom.length) return
        // for (let index = 0; index < dom.length; index++) {
        //   let element = dom[index]
        //   element.style.display = 'none'
        // }
        // dom =document.getElementsByClassName('el-picker-panel')
        // if(!dom.length) return
        // for (let index = 0; index < dom.length; index++) {
        //   let element = dom[index]
        //   element.style.display = 'none'
        // }
      // }
    },
    getUserInfo() {
      let _this = this;
      // this.menus.forEach(function(first, fIndex) {
      //   // 重置菜单数组的key，element-ui 的导航栏必备
      //   let firstKey = parseInt(fIndex + 1);
      //   first.key = firstKey + "";
      //   first.subMenus.forEach(function(second, sIndex) {
      //     let secondKey = parseInt(sIndex + 1);
      //     second.key = firstKey + "-" + secondKey;
      //   });
      // });
      getUserData().then(res => {
        // 工作台传参
        if (self != top) {
          const lt = window.location;
          // console.log(window.parent.href, 'qwertyh');
          // 页面传参
          window.parent.postMessage(res.data, '*');
        }
       // 转换user接口数据
       res.data =  window.$$global.userInterfaceConversion(res.data)

        if (res.data.permissions.menu_numberManagement || res.data.permissions.menu_customerManagement){
          res.data.permissions.menu_objConfig={value:true};
          // debugger
          // 工号显示
        }

        // 根据网点权限设置菜单
        let tempMenuList;
        _this.menus.forEach(function(first) {
          // 根据权限筛选菜单
          first.subMenus = first.subMenus.filter(function(second) {
            let tempMark = second.path.substring(1);
            if(tempMark == 'guide' ){
              return true
            }else{
              return (
               res.data.permissions.admin ||
               res.data.permissions['menu_' + tempMark]
              );
            }
          });
        });
        // console.log('权限',res.data.permissions.menu_numberManagement);

        // console.log(res.data.permissions);
        // debugger

        tempMenuList = _this.menus.filter(function(item) {
          // 除去菜单中，二级菜单为空的一级菜单
          return item.subMenus.length > 0;
        });
        let sourceMenu = sourceMenuTour;
        let specialMenu = [
          '/customerManagement',
          '/settlement',
          '/numberManagement',
        ];
        let specialNum = 0;
        tempMenuList.forEach(function(first, fIndex) {
          //重置菜单数组的key，element-ui 的导航栏必备
          let firstKey = parseInt(fIndex + 1) + '';
          first.key = firstKey + '';
          sourceMenu.forEach(item => {
            if (item.name === first.name) {
              item.key = firstKey;
            }
          });
          first.subMenus.forEach(function(second, sIndex) {
            let secondKey = parseInt(sIndex + 1) + '';
            second.key = firstKey + '-' + secondKey;
            if (specialMenu.some(scItem => scItem === second.path)) {
              specialNum++;
            }
            sourceMenu.forEach(item => {
              if (item.name === second.title) {
                item.key = firstKey + '-' + secondKey;
              }
            });
          });
        });

        if (specialNum < 3) {
          sourceMenu = sourceMenu.filter(
            item => item.element !== '.numberManagementTour'
          );
        }
        sourceMenu = sourceMenu.filter(item => item.key);
        this.targetMenu = sourceMenu.sort(function(s, t) {
          return s.order - t.order;
        });

        let domain_url = window.location.host;
        let permissions = res.data.permissions ? res.data.permissions : {};
        if (domain_url != 'sz.zt-express.com') {
          const newArr = {
            name: '测试菜单',
            key: String(tempMenuList.length + 1),
            subMenus: [
              {
                key: String(tempMenuList.length + 1) + '-1',
                title: '※收中转费(测试)',
                path: '/sendReceiveFeeTest',
              },
            ],
          };
          permissions['menu_sendReceiveFeeTest'] = {value: true};
          tempMenuList.push(newArr);
        }
        _this.$store.commit('setLimitMenus', tempMenuList); // 将筛选后的菜单 提交到 menus vue-x数据仓库中
        _this.z_param.permissions = permissions;
        _this.z_param.permissions['menu_guide'] = true
        let info = {};
        if (res.data.permissions.admin) {
          info.admin = true; // 判断是否是超管权限
        } else {
          info.admin = false;
        }
        if (res.data.permissions['switchSite']) {
          info.showSwitchButton = true;
        } else {
          info.showSwitchButton = false; // 是否显示切换网点 按钮
        }
        info.siteInfo = res.data.siteInfo || null;
        info.siteProv = res.data.siteInfo.prov ? res.data.siteInfo.prov : null; // 当前登录的网点的省份
        info.siteCity = res.data.siteInfo.city ? res.data.siteInfo.city : null; // 当前登录的网点的城市
        info.siteName = res.data.siteInfo.siteName
          ? res.data.siteInfo.siteName
          : null; // 当前登录的网点的name
        info.fullName = res.data.userName ? res.data.userName : null; // 员工名称
        info.userCode = res.data.userCode ? res.data.userCode : null; // 员工编号
        info.avatar = res.data.avatar ? res.data.avatar : null; // 员工头像地址
        info.node_name = res.data.siteName ? res.data.siteName : null; // 员工所在网点的名称
        info.dept_name = res.data.deptName ? res.data.deptName : null; // 员工所在部门名称
        _this.$store.commit('saveUser', info);
        _this.z_param.avgStatus = res.data.avgStatus
          ? !!res.data.avgStatus
          : false;
        _this.z_param.admin = info.admin;
        _this.z_param.userType = res.data.userType;
        _this.z_param.accountId = res.data.accountId;
        _this.z_param.fullname = res.data.fullName;
        _this.z_param.hasSubordinate = res.data.hasSubordinate;
        _this.z_param.siteInfo = res.data.siteInfo;
        setTimeout(() => {
          _this.$store.commit('setLoginStatus', true);
        });
        window.localStorage.setItem('siteInfo', JSON.stringify(res.data.siteInfo))
        let areaCode = (res.data.siteInfo && res.data.siteInfo.siteId) || null;
        localStorage.setItem('areaCode', areaCode);

        // 切换网点后自动刷新
        let shenzhou_loginInfo = res.data.userId + ';' + res.data.siteInfo.siteId;
        window.localStorage.setItem('shenzhou_loginInfo', shenzhou_loginInfo);
        // 启动一个异步程序定时检查是否有变化，如果被其他页面修改了这个值，则刷新页面
        let checkLoginInfoChange = function(oldVal) {
          let newVal = window.localStorage.getItem('shenzhou_loginInfo');
          if (oldVal + '' == newVal + '') {
            setTimeout(function() {
              checkLoginInfoChange(oldVal);
            }, 1000);
          } else {
            window.location.reload();
          }
        };
        checkLoginInfoChange(shenzhou_loginInfo);
      })
    },
    reLayout() {
      intro && intro.refresh();
      this.$store.commit('setClientHeight', document.body.clientHeight);
    },
  },
};
</script>

<style lang="scss">
