  position: relative;
  height: 100%;
}

.no-pd0 {
  .less-width {
    padding-left: 0;
    padding-top: 0;
  }
}

.page-container {
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: auto;

  padding: 60px 10px 10px;
  background: #eef0f6;

  transition: all 400ms;
}

.widther {
  padding-left: 76px;
  /*margin-left: 76px;*/
}

.less-width {
  padding-left: 190px;
  /*margin-left: 190px;*/
}

.el-loading-mask {
  background-color: rgba(0, 0, 0, 0.6) !important;
}

.loading-ball {
  height: 100%;
  width: 100%;
  text-align: center;
  background: #fff;
  .ball-loader {
    margin-top: 10%;
  }
}

.page-container > main {
  height: 100%;
}

.content-box {
  border-top: 10px solid #e9eef2;
  position: relative;
  padding-bottom: 20px;
  .btn-box {
    padding: 10px 10px 0;
    display: flex;
    justify-content: flex-end;
  }
  .page-box {
    text-align: right;
    position: absolute;
    bottom: -2px;
    right: 10px;
  }
}

.search-box .datePicker .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 135px;
}

.search-box .dateTimePicker .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 185px;
}

.search-box .el-input--small .el-input__inner {
  font-size: 12px;
}

.search-box .el-select-dropdown__item {
  font-size: 13px;
}

.form-input .el-select {
  width: 100%;
}

.el-message-box__btns .el-button--default.el-button--primary {
  background: #409eff;
}
