import axios from 'axios';
import qs from 'qs';
import {
  Message
} from 'element-ui';
import store from '../store/store';

let LOADING_ERR = 0; //控制错误信息只弹出一次
const HTTP_ERROR_MSG = '请求超时，请重试';
axios.defaults.headers.post['Content-Type'] =
  'application/x-www-form-urlencoded';
axios.defaults.withCredentials = true
const xTag = 'fat2'
  // 网关模式需要
  const { HostName, getHeader, interception301, dateConversion,urlToObj } = window.$$global
  function configConversion(config){
    let {url,data} = config
    const {method,headers,params} = config
    if(url.indexOf('?') > -1){
      data = urlToObj(url)
      url = url.split('?')[0]
    }else if(method == 'get'){
       data = params    
    }
    const header = getHeader(url,xTag)
    const modeUrl = url.indexOf('http') > -1?url: HostName + url
    config = {
      ...config,
      method:method == 'get'?'post':method,
      params:'',
      url: modeUrl,
      data:dateConversion(qs.parse(data)),
      headers:{
        ...headers,
        ...header,
        // 'x-tag':'wfs'
      }
    }
    return config;
  }
//请求拦截器

axios.interceptors.request.use(
  function (config) {
    return configConversion(config)
  },
  function (error) {
    return Promise.reject(error);
  }
);
const errMsg = '服务器繁忙，请输入更精准的查询条件!'
const resultMsg = '请求超时'
//返回拦截器
axios.interceptors.response.use(
  res => {   
    window.$$global.removeNull(res.data.result) 
    if(res.data.result != undefined){
      res.data.data = res.data.result
    }
    if (res.data.statusCode == 301 || res.data.code == 301) {
      interception301(res.data.message)
      return 
    }
    if(res.config.url.indexOf("list") > -1 || res.config.url.indexOf("page") > -1 || res.config.url.indexOf("query") > -1){
      try {
        const {message} = res.data 
        res.data.message = message == resultMsg ? errMsg : message
      } catch(err){
        console.log(err);
      }
    }
    res.data.code = res.data.status ? 0 : -1
    res.data.statusCode = res.data.code    
    setTimeout(() => {
      store.commit('setLoading', false);
    }, 1000);
    let statusCode = res.data.statusCode;
    let code = res.data.code;
    if (statusCode === 0 || code === 0) {} else if (statusCode == 10001 || code == 10001) {
      Message.error({
        message: '喔哦，权限不够哟，请联系你的老板添加！',
        duration: 3000,
      });
      return;
    }
    setTimeout(() => {
      store.commit('setLoading', false);
    }, 1000);
    return Promise.resolve(res);
  },
  error => {
    store.commit('setLoading', false);
    if (error.response.status) {
      if (LOADING_ERR === 0) {
        LOADING_ERR = 1;
        Message.error({
          message: HTTP_ERROR_MSG,
          duration: 2800,
          showClose: true,
          onClose: function () {
            LOADING_ERR = 0;
          },
        });
      }
      return Promise.reject(error.response);
    }
  }
);

export const get = (url, params, callError = true, showLoading = true) => {
  if (showLoading) store.commit('setLoading', true);
  return new Promise((resolve, reject) => {
    axios
      .get(url,{
        params:params
      })
      .then(
        response => {
          if (response.data.code !== 0) {
            if (LOADING_ERR === 0 && callError) {
              LOADING_ERR = 1;
              Message.error({
                message: response.data.message,
                duration: 2800,
                showClose: true,
                onClose: function () {
                  LOADING_ERR = 0;
                },
              });
            }
          }
          resolve(response.data);
        },
        err => {
          if (err.data.code != 0) {
            if (LOADING_ERR === 0 && callError) {
              LOADING_ERR = 1;
              Message.error({
                message: err.data.message,
                duration: 2800,
                showClose: true,
                onClose: function () {
                  LOADING_ERR = 0;
                },
              });
            }
          } else {
            if (LOADING_ERR === 0 && callError) {
              LOADING_ERR = 1;
              Message.error({
                message: HTTP_ERROR_MSG,
                duration: 2800,
                onClose: function () {
                  LOADING_ERR = 0;
                },
              });
            }
          }
          setTimeout(() => {
            store.commit('setLoading', false);
          }, 1000);
          reject(err);
        }
      )
      .catch(error => {
        if (LOADING_ERR === 0 && callError) {
          LOADING_ERR = 1;
          Message.error({
            message: HTTP_ERROR_MSG,
            duration: 2800,
            showClose: true,
            onClose: function () {
              LOADING_ERR = 0;
            },
          });
        }
        reject(error);
      });
  });
};

