let state = {
  userInfo: {
    admin: '',
    siteInfo: '',
    siteProv: '',
    siteCity: '',
    siteName: '',
    fullName: '',
    userCode: '',
    avatar: '',
    node_name: '',
    dept_name: '',
    showSwitchButton: ''
  },
  isLogin: false
};
let getters = {
  info(state){
    return state.userInfo
  },
  isLogin(state){
    return state.isLogin
  }
};
let mutations = {
  saveUser(state,obj){
    state.userInfo = obj;
  },
  setLoginStatus(commit, value){
    state.isLogin = value;
  }
};
export default {state,getters,mutations}
