<template>
  <div class="sz-settlement-component del-arrow-up">
    <el-select
      ref="szCustomerSelect"
      v-model="inputValue"
      :clearable="clearable"
      :disabled="disabled"
      :filterable="true"
      :filter-method="remote"
      @change="changeItem"
      @clear="clear"
      @blur="blur"
      no-match-text=" "
      :placeholder="placeholder"
      :popperClass="popperClass"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="item.feeName"
        :value="item.id"
      />
    </el-select>
    <!-- :filterable="true"
      :filter-method="remote" -->
  </div>
</template>

<script>
/**
 * v-model返回的是结算对象的id
 * model事件返回的是选中结算对象的整个列
 * 回填根据id回调值，可能暂时没有这个，所以可能回填失败
 * clearable是否可以清空
 */
import { getcostSubtypesAjax } from "@/config/api";
export default {
  props: {
    value: {
      type: [Number, String]
    },
    default: [Number, String],
    clearable: {
      type: Boolean,
      default: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    popperClass: {
      //acconunt-name-class
      type: String,
      default: ""
    }
  },
  data() {
    return {
      inputValue: "",
      options: [],
      globalOptions: []
    };
  },
  mounted() {
    this.getSettlement();
  },
  methods: {
    // 远程搜索方法
    remote: _.debounce(function(key) {
      this.getSettlement(key);
    }, 1000),
    // 获取结算对象
    getSettlement(key) {
      let options = {
        feeName: key
      };
      getcostSubtypesAjax(options)
        .then(res => {
          if (res.data && res.data.length > 0) {
            this.options = res.data;
            if (this.globalOptions.length === 0) {
              this.globalOptions = res.data;
            }
          } else {
            // this.msg("没有查询到数据");
            this.options = [];
          }
        })
        .catch(response => {});
    },
    // 将得到的值抛出去
    changeItem(key) {
      let tempObj = this.options.filter(function(ele) {
        return ele.id == key ? true : false;
      });
      let chooseObj = tempObj[0] ? tempObj[0] : null;
      this.$emit("input", this.inputValue);
      this.$emit("model", this.chooseObj);
    },
    blur() {
      if (this.options.length === 0) {
        this.options = this.globalOptions;
      }
    },
    autoBlur() {
      if (this.$refs.szCustomerSelect) this.$refs.szCustomerSelect.visible = false;
    },
    clear() {
      this.inputValue = "";
      this.options = this.globalOptions;
      // this.getSettlement();
    },
    msg(val) {
      this.$message({
        showClose: true,
        message: val,
        type: "warning",
        duration: 5000
      });
    }
  },
  watch: {
    value(value, oldValue) {
      this.inputValue = value;
      this.$emit("input", value);
    }
  }
};
</script>
