import options from '../../config/data'

let noneMenu = [{
  name: '没有权限访问',
  key: '1',
}];

let state = {storeMenus: options.menus};
let getters = {
  menus(state) {
    return state.storeMenus;
  }
};
let mutations = {
  setMenusNone(state, status) {
    if (status === true) {
      state.storeMenus = noneMenu;
    }
  },
  setLimitMenus(state, list) {
    state.storeMenus = list;
  },
};
export default {state, getters, mutations};
