<template>
  <div
    class="z-pagination"
    v-if="total > 0"
  >
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        :total="total"
        :page-sizes="pageSizes"
        :layout="layout"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "szPagination",
  props: {
    page: {
      type: Number,
      default: 1
    },
    pageSize: {
      type: Number,
      default: 100
    },
    total: {
      type: Number,
      default: 0
    },
    pageSizes: {
      type: Array,
      default: function() {
        return [100, 200, 500, 1000, 2000, 5000];
      }
    },
    layout: {
      type: String,
      default: "total, sizes, prev, pager, next"
    }
  },
  data() {
    return {
      currentPage: this.page,
      pageClone:this.page
    };
  },
  watch: {
    page(val, oldVal) {
      if (val) {
        this.currentPage = this.page;
        this.pageClone = this.page
      }
    }
  },
  methods: {
    handleSizeChange(size) {
      this.$emit("fetchData", { pageSize: size });
    },
    handleCurrentChange(currentPage) {
      // console.log(currentPage);
      if (currentPage > 10) {        
        this.currentPage = this.pageClone < 10?this.pageClone:10
        this.$message({
          message: "分页太多啦，建议直接下载或精确查询条件！",
          showClose: true,
          type: "warning"
        });
        return;
      }
      this.pageClone = currentPage
      this.$emit("fetchData", { page: currentPage });
    }
  }
};
</script>
