<template>
  <div class="asyncExport">
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :modal-append-to-body="false"
        width="650px"
        center
      >
        <div>
          <div class="title">温馨提示：系统只保留近3天的导出数据，下载完成后请及时查看！</div>
          <div>
            <el-transfer
              ref="transferRef"
              style="text-align: left; display: inline-block;padding:10px;"
              v-model="value"
              filterable
              :titles="['可选列', '已选列']"
              :button-texts="['到左边', '到右边']"
              :format="{
                noChecked: '${total}',
                hasChecked: '${checked}/${total}'
              }"
              @change="handleChange"
              @right-check-change = "rightCheckAction"
              :right-default-checked="rightCheck"
              :data="data"
              target-order="push"
              >
            </el-transfer>
            <div class="up-down">
              <el-button type="primary" class="btn" :disabled="rightCheckLength !== 1" @click="moveAction(1)">置顶</el-button>
              <el-button type="primary" class="btn" :disabled="rightCheckLength !== 1" @click="moveAction(2)">上移一位</el-button>
              <el-button type="primary" class="btn" :disabled="rightCheckLength !== 1" @click="moveAction(3)">下移一位</el-button>
              <el-button type="primary" class="btn" :disabled="rightCheckLength !== 1" @click="moveAction(4)">置底</el-button>
            </div>
          </div>
          <div class="bottomInput">
            <label style="color: red;position: relative;top: 3px;">*</label>
            <label style="padding-right:16px;">请输入任务名</label>
            <el-input v-model.trim="input" placeholder="请输入内容" maxlength="20"></el-input>
          </div>
        </div>
        <div
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="handleClose" style="padding-left:48px;padding-right:48px">取 消</el-button>
          <el-button  type="primary" :disabled="value.length === 0 || input == '' " @click="exportAction" >添加至下载中心</el-button>
        </div>
      </el-dialog>
      <div id="task-ball1" ref="taskBallH" style="display:none;width: 15px;height: 15px;background: red;border-radius: 15px;position: absolute;top: 50%;left:45%;z-index:9999;"></div>
      <div id="task-icon2" ref="taskIconH" style="width: 15px;height: 15px;border-radius: 15px;position: absolute;top: -40px;right:150px;z-index:999999;"></div>
      <div id="task-icon3" ref="taskIconHW" style="width: 15px;height: 15px;border-radius: 15px;position: absolute;top: -40px;right:290px;z-index:999999;"></div>
  </div>
</template>
<script>
import { cloneDeep } from "lodash";
import {get,post,postJson} from '@/config/axios.config'
import { debug, debuglog } from 'util';
export default {
  props: {
    exportData:{
      type: [Object, Array],
      default: () => []
    },
    exportName: {
      type: [Number, String],
      required: true,
      default: ""
    },
    exportUrl: {
      type: [Number, String],
      required: true,
      default: ""
    },
    exportOption:{
      type: [Object, Array, String],
      default: () => {}
    },
  },
  data() {
    return {
      dialogVisible:false,
      input:"",
      data:[],
      title:"导出",
      value: [],
      rightCheckLength:0,
      rightCheck:[]
    };
  },
   watch: {
    input: {
      handler(val, oldVal) {
        if(val){
          this.input = this.input.replace(/[^a-zA-Z0-9\u4e00-\u9fa5]/g,"");
        }
      },
      deep: true
    },
    value:{
      handler(val, oldVal) {
        if(val.length !== oldVal.length){
          this.rightCheckLength = 0;
        }
      },
      deep: true
    }
  },
  methods: {
    // //抛物线动画
    funParabola(element, target, options) {
        let defaults = {
            speed: 166.67, // 每帧移动的像素大小，每帧（对于大部分显示屏）大约16~17毫秒
            curvature: 0.001,  // 实际指焦点到准线的距离，你可以抽象成曲率，这里模拟扔物体的抛物线，因此是开口向下的
            progress: function() {},
            complete: function() {}
        };
        var params = {}; options = options || {};
        for (var key in defaults) {
            params[key] = options[key] || defaults[key];
        }
        var exports = {
            mark: function() { return this; },
            position: function() { return this; },
            move: function() { return this; },
            init: function() { return this; }
        };
        var moveStyle = "margin", testDiv = document.createElement("div");
        if ("oninput" in testDiv) {
            ["", "ms", "webkit"].forEach(function(prefix) {
                var transform = prefix + (prefix? "T": "t") + "ransform";
                if (transform in testDiv.style) {
                    moveStyle = transform;
                }
            });
        }
        // 根据两点坐标以及曲率确定运动曲线函数（也就是确定a, b的值）
        /* 公式： y = a*x*x + b*x + c;*/
        var a = params.curvature, b = 0, c = 0;
        // 是否执行运动的标志量
        var flagMove = true;
        if (element && target && element.nodeType == 1 && target.nodeType == 1) {
            var rectElement = {}, rectTarget = {};
            // 移动元素的中心点位置，目标元素的中心点位置
            var centerElement = {}, centerTarget = {};
            // 目标元素的坐标位置
            var coordElement = {}, coordTarget = {};
            // 标注当前元素的坐标
            exports.mark = function() {
                if (flagMove == false) return this;
                if (typeof coordElement.x == "undefined") this.position();
                element.setAttribute("data-center", [coordElement.x, coordElement.y].join());
                target.setAttribute("data-center", [coordTarget.x, coordTarget.y].join());
                return this;
            }
            exports.position = function() {
                if (flagMove == false) return this;
                var scrollLeft = document.documentElement.scrollLeft || document.body.scrollLeft,
                  scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
                // 初始位置
                if (moveStyle == "margin") {
                    element.style.marginLeft = element.style.marginTop = "0px";
                } else {
                    element.style[moveStyle] = "translate(0, 0)";
                }
                // 四边缘的坐标
                rectElement = element.getBoundingClientRect();
                rectTarget = target.getBoundingClientRect();
                // 移动元素的中心点坐标
                centerElement = {
                    x: rectElement.left + (rectElement.right - rectElement.left) / 2 + scrollLeft,
                    y: rectElement.top + (rectElement.bottom - rectElement.top) / 2	+ scrollTop
                };
                // 目标元素的中心点位置
                centerTarget = {
                    x: rectTarget.left + (rectTarget.right - rectTarget.left) / 2 + scrollLeft,
                    y: rectTarget.top + (rectTarget.bottom - rectTarget.top) / 2 + scrollTop
                };
                // 转换成相对坐标位置
                coordElement = {
                    x: 0,
                    y: 0
                };
                coordTarget = {
                    x: -1 * (centerElement.x - centerTarget.x),
                    y:  -1 * (centerElement.y - centerTarget.y)
                };
                b = (coordTarget.y - a * coordTarget.x * coordTarget.x) / coordTarget.x;
                return this;
            };
            // 按照这个曲线运动
            exports.move = function() {
                // 如果曲线运动还没有结束，不再执行新的运动
                if (flagMove == false) return this;
                var startx = 0, rate = coordTarget.x > 0? 1: -1;
                var step = function() {
                    // 切线 y'=2ax+b
                    var tangent = 2 * a * startx + b; // = y / x
                    // y*y + x*x = speed
                    // (tangent * x)^2 + x*x = speed
                    // x = Math.sqr(speed / (tangent * tangent + 1));
                    startx = startx + rate * Math.sqrt(params.speed / (tangent * tangent + 1));
                    // 防止过界
                    if ((rate == 1 && startx > coordTarget.x) || (rate == -1 && startx < coordTarget.x)) {
                        startx = coordTarget.x;
                    }
                    var x = startx, y = a * x * x + b * x;
                    // x, y目前是坐标，需要转换成定位的像素值
                    if (moveStyle == "margin") {
                        element.style.marginLeft = x + "px";
                        element.style.marginTop = y + "px";
                    } else {
                        element.style[moveStyle] = "translate("+ [x + "px", y + "px"].join() +")";
                    }
                    if (startx !== coordTarget.x) {
                        params.progress(x, y);
                        window.requestAnimationFrame(step);
                    } else {
                        // 运动结束，回调执行
                        params.complete();
                        flagMove = true;
                    }
                };
                window.requestAnimationFrame(step);
                flagMove = false;
                return this;
            };
            // 初始化方法
            exports.init = function() {
                this.position().mark().move();
            };
        }
        return exports;
    },
    export(){
      // let fly = document.getElementById('task-ball1');
      // fly.style.top = '50%';
      // fly.style.left = '50%';
      // fly.style.display = "none";
      this.$refs.taskBallH.style.left = "50%";
      this.$refs.taskBallH.style.top = "50%";
      this.$refs.taskBallH.style.display = "none";
      this.data = [];
      this.exportData.forEach((item,index) => {
        if(item.displayName != "选择"){
          this.data.push({
            key: index,
            label: item.displayName
          });
        }
      });
      this.dialogVisible = true;
      this.value = [];
      this.rightCheckLength=0;
      this.rightCheck=[];
      this.input = "";
    },
    handleClose(done) {
      this.data = [];
      this.$refs.transferRef.$refs.leftPanel.query = '';
      this.$refs.transferRef.$refs.rightPanel.query = '';
      this.dialogVisible = false;
      if(done == "ok"){
        let cart = "";
        // let fly = document.getElementById('task-ball1');
        let fly = this.$refs.taskBallH;
        if(this.z_param.isActiveH){
          cart = this.$refs.taskIconHW;
        }else{
           if (this.z_param.isNode) {
            cart = document.getElementById('task-icon1');
          }else{
            // cart = document.getElementById('task-icon2');
            cart = this.$refs.taskIconH;
          }
        }
       
        fly.style.display = 'block';
        this.funParabola(fly, cart,{
          speed: 400,
          curvature: 0.0008,
          complete: function() {
            fly.style.top = 0 + 'px';
            fly.style.left = 0 + 'px';
          }
        }).init();
      }
    },
    handleChange(val, direction, movedKeys) {
    },
    rightCheckAction(val, direction) {
      this.rightCheckLength = val.length;
      this.rightCheck = val;
    },
    swapItems(arr, index1, index2,direction) {
      if(direction=='up'){//置顶
      arr.unshift(arr[index1]);
      arr.splice(index1+1,1);
      return arr;
      }
      if(direction=='down'){//置底
      arr.push(arr[index1]);
      arr.splice(index1,1);
      return arr;
      }
      arr[index1] = arr.splice(index2, 1, arr[index1])[0];
      return arr;
    },
    moveAction(val){
      let nub = 0
      this.value.some((v,i)=>{
        if(v === this.rightCheck[0]){
          nub = i;
          return true;
        }
      });
      let newVal = cloneDeep(this.value);
      if(val === 1){
        if(nub !== 0){
          newVal = this.swapItems(newVal, nub,0,'up');
          this.rightCheck[0] = newVal[0];
        }else{
          this.msg("已置顶,无法再上移");
        }
      }else if(val === 2){
        if(nub !== 0){
          newVal = this.swapItems(newVal, nub, nub - 1);
          this.rightCheck[0] = newVal[nub-1];
        }else{
          this.msg("已置顶,无法再上移");
        }
      }else if(val === 3){
        if(nub !== this.value.length-1){
          newVal = this.swapItems(newVal, nub, nub + 1);
          this.rightCheck[0] = newVal[nub+1];
        }else{
          this.msg("已置底,无法再下移");
        }
      }else if(val === 4){
        if(nub !== this.value.length-1){
          newVal = this.swapItems(newVal, nub,0,'down');
          this.rightCheck[0] = newVal[this.value.length-1];
        }else{
          this.msg("已置底,无法再下移");
        }
      }
      this.value = newVal;
    },
    msg(val) {
      this.$message({
        showClose: true,
        message: val,
        type: 'warning',
        duration: 2000,
      });
    },
    exportAction(){
      let options=[];
      this.value.forEach((item)=>{
        options.push({
          "property":this.exportData[item].name,
          "title":this.exportData[item].displayName
        });
      });
      let option={
        ...this.exportOption,
        jobName:this.input,
        menuName:this.exportName,
        titles:JSON.stringify(options)
      }
      post(this.exportUrl,option,false,false).then(res => {
        if(res.code === 0){
          this.handleClose("ok");
        }else{
          this.msg(res.message);
        }
      })
      .catch(e => {
        this.msg(e.message);
      });
    }
  }
};
</script>
