    }
  }
}
</style>

<style lang="scss">
.progress-button {
  position: relative;
  display: inline-block;
  padding: 0;
  text-align: center;
  width: 150px;
  outline: none;
  border: none;
  background-color: #67c23a;
  color: #fff;
  height: 38px;
  line-height: 38px;
  letter-spacing: 1px;
  font-size: 14px;
  -webkit-appearance: button;
  cursor: pointer;
  .content {
    z-index: 10;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    position: relative;
    display: block;
