<template>
  <div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :modal-append-to-body="false"
      :width="width"
      :height="height"
      center
    >
      <div>
        <el-table
          :data="impData"
          stripe
          height="300"
          border
        >
          <el-table-column
            v-for="(column, index) in columns"
            :key="index"
            :prop="column.name"
            :label="column.displayName"
            :min-width="column.minWidth ? column.minWidth : 120"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            label="结果"
            width="260"
            prop="bak"
          >
            <template slot-scope="scope">
              <div
                v-if="scope.row.bak === '成功'"
                style="color: #67c23a;font-size:20px;"
              >
                <i class="el-message__icon el-icon-success"></i>
              </div>
              <div
                v-else-if="scope.row.bak === '失败'"
                style="color: #f56c6c;font-size:20px;"
              >
                <i class="el-message__icon el-icon-error"></i>
              </div>
              <div
                v-else
                style="color: #f56c6c;"
              >
                {{scope.row.bak}}
              </div>
            </template>
          </el-table-column>
        </el-table>
        <p>
          共
          <span>{{totalNum}}</span>
          条，成功
          <span>{{successNum}}</span>
          条，失败
          <span>{{failedNum}}</span>
          条，
          <span>{{totalNum-count}}</span>条未处理
        </p>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <button class="progress-button">
          <span
            class="content"
            @click="start"
            id="content"
          >{{btnText}}</span>
          <span class="progress">
            <span
              class="progress-inner notransition"
              :style="{width:progress+'%'}"
            ></span>
          </span>
        </button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {get,post,postJson} from '@/config/axios.config'
import { debug } from 'util';
export default {
  props: {
    title: {
      type: [Number, String],
      default: ""
    },
    userId: {
      type: [Number, String],
      default: ""
    },
    width: {
      type: [Number, String],
      default: "700px"
    },
    height: {
      type: [Number, String],
      default: "500px"
    },
    dialogVisible: {
      type: Boolean,
      default: false
    },
    impDataNew: {
      type: [Object, Array],
      default: () => {}
    },
    optionsH: {
      type: [Object, Array],
      default: () => {}
    },
    columns: {
      type: [Object, Array],
      default: () => {}
    },
    importUrl: {
      type: String,
      default: ""
    },
    totalNum: { type: [Number, String], default: 0 },
    types: {
      type: String,
      default: ""
    },
  },
  data() {
    return {
      successNum: 0,
      failedNum: 0,
      count: 0,
      status: 0,
      btnText: "开始",
      impData: []
    };
  },
  methods: {
    handleClose(done) {
      if(this.btnText == "已结束"){
        this.$emit("model", "");
        this.handleClose2();
      }else{
        if(this.btnText == "开始"){
          this.$emit("model", "noSearch");
          this.handleClose2();
          return;
        }
        if(this.btnText == "暂停"){
          document.getElementById("content").click();
        }
        this.$confirm('此操作将停止执行, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$emit("model", "");
          this.handleClose2();
        }).catch(() => {});
      }
    },
    start(val) {
      if (val.target.innerText == "已结束") {
        this.handleClose2();
        this.$emit("model", "");
      } else {
        if (this.impData && this.impData.length > 0) {
          if (this.status === 0) {
            this.btnText = "暂停";
            this.status = 1;
            this.impData[this.count]["bak"] = "处理中...";
            this.processing = true; // 保证按顺序执行请求
            this._importOneRow(this.impData[this.count]);
          } else if (this.status === 1) {
            this.status = 2;
            this.btnText = "继续";
          } else if (this.status === 2) {
            this.status = 1;
            this.btnText = "暂停";
            if (!this.processing) {
              this.impData[this.count]["bak"] = "处理中...";
              this.processing = true;
              this._importOneRow(this.impData[this.count]);
            }
          }
        }
      }
    },
    _importOneRow(row) {
      let isQuest = true;
      let options = {};
      if(this.title == "加预付款" || this.title == "线下返款" || this.title == "取消加款" || this.title == "重新计算" || this.title == "批量同步账单结算对象" || this.title == "批量同步签收状态"){
        options = {
          billCode: row.billCode
        };
      }else if(this.title == "编辑" || this.title == "批量编辑"){
        options = {...this.optionsH,billCode: row.billCode,};
      }else if(this.types  === "addedWeightFeeS"){
        isQuest = false;
        options.accountUserId = row.accountUserId;
        options.signDate = row.signDate;
        options.dealStatus = row.hexiaoStatus == 1?1: 0;
        if(this.title == "批量取消扣款处理结果") { // 取消扣款
            options.dealType = 3;
            options.dealFee = row.hexiaoFee;
        }else if(this.title == "批量扣派费差额处理结果") {  // 扣派费差额
            options.dealType = 2;
            options.dealFee = row.subFee;
        }else if(this.title == "批量扣续重派费处理结果") {  
            options.dealType = 1;
            options.dealFee = row.addDispFee;
        }
      }else if(this.types  === "twoTransferFeeS"){
        isQuest = false;
        options = {...row};
      }
      if (isQuest) {
        post(this.importUrl,options,false,false).then(res => {
          if(res.code === 0){
            this._success(res);
          }else{
            this._failActionPt(res);
          }
        })
        .catch(e => {
          this._failActionPt(e);
        });
      } else {
        // this._success2("");
        postJson(this.importUrl,options,false,false).then(res => {
          if(res.code === 0){
            this._success(res);
          }else{
            this._failActionPt(res);
          }
        })
        .catch(e => {
          this._failActionPt(e);
        });
      }
    },
    _success(r) {
      this.impData[this.count]["bak"] = "成功";
      this.count++;
      this.successNum++;
      this.processing = false;
      if (this.status === 1) {
        if (this.impData.length > this.count) {
          this.impData[this.count]["bak"] = "处理中...";
          this.impData = this.impData.slice(0);
          this._importOneRow(this.impData[this.count]);
        } else {
          this.impData = this.impData.slice(0);
          this.status = 3;
          this.btnText = "已结束";
        }
      }
    },
    // _success2(r) {
    //   this.impData[this.count]["bak"] = r;
    //   this.count++;
    //   this.successNum++;
    //   this.processing = false;
    //   if (this.status === 1) {
    //     if (this.impData.length > this.count) {
    //       this.impData[this.count]["bak"] = "处理中...";
    //       this.impData = this.impData.slice(0);
    //       this._importOneRow(this.impData[this.count]);
    //     } else {
    //       this.impData = this.impData.slice(0);
    //       this.status = 3;
    //       this.btnText = "已结束";
    //     }
    //   }
    // },
    _failActionPt(r) {
      try {
        if(r.data){
           this.impData[this.count]["bak"] = r.data.message + "";
        }else{
          this.impData[this.count]["bak"] = r.message + "";
        }
      } finally {
        this._failActionHandler();
      }
    },
    _failActionHandler() {
      this.count++;
      this.failedNum++;
      this.processing = false;
      if (this.status === 1) {
        if (this.impData.length > this.count) {
          this.impData[this.count]["bak"] = "处理中...";
          this.impData = this.impData.slice(0);
          this._importOneRow(this.impData[this.count]);
        } else {
          this.impData = this.impData.slice(0);
          this.status = 3;
          this.btnText = "已结束";
        }
      }
    },
    handleClose2() {
      this.successNum = 0;
      this.failedNum = 0;
      this.count = 0;
      this.status = 0;
      this.btnText = "开始";
    }
  },
  computed: {
    progress() {
      return (this.count * 100) / this.totalNum;
    }
  },
  watch: {
    impDataNew(val) {
      this.impData = val;
    }
  }
};
</script>
<style>
.el-dialog__headerbtn {
  top: 13px;
}
</style>

<style lang="scss" scoped>
.progress-button {
  position: relative;
  display: inline-block;
  padding: 0;
  text-align: center;
  width: 150px;
  outline: none;
  border: none;
  background-color: #67c23a;
  color: #fff;
  height: 38px;
  line-height: 38px;
  letter-spacing: 1px;
  font-size: 14px;
  -webkit-appearance: button;
  cursor: pointer;
  .content {
    z-index: 10;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    position: relative;
    display: block;
  }
  .progress-inner {
    position: absolute;
    left: 0;
    background: #dd6161;
    top: 0;
    width: 0;
    height: 100%;
  }
}
</style>
