<template>
  <div class='batch-box'>
    <el-dialog
      :title="title"
      v-drag-dialog
      :modal-append-to-body="false"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :width="width + 'px'"
      :before-close="handleClose">
      <div
        class="batch-content-box"
        :style="{width: width ? width + 'px' : '700px',height: height ? height + 'px' : '500px'}"
      >
        <div class="content-center">
          <div class="line">
            <el-progress
              :text-inside="true"
              :stroke-width="14"
              :percentage="progress"
              status="success"
            ></el-progress>
          </div>
          <div class="width-full">
            <div class="scroll-table-box">
              <table class="table-head">
                <tr>
                  <td
                    v-for="item in columns"
                    :key="item.index"
                    :style="{width: item.size + 'px'}"
                  >{{item.displayName}}</td>
                  <td style="width: 250px">处理结果</td>
                </tr>
              </table>
              <div
                :style="{height: (height - 174) + 'px'}"
                class="table-body-box"
              >
                <table class="table-body">
                  <tr
                    v-for="item in impData"
                    :key="item.index"
                  >
                    <td
                      v-for="column in columns"
                      :key="column.index"
                      :style="{width: column.size + 'px'}"
                    >{{item[column.name]}}</td>
                    <td style="width: 250px">
                      <div v-if="item.batchResultCode===0">
                        <i class="el-icon-success"></i>
                        <span :title="item.msg">{{item.msg || '成功'}}</span>
                      </div>
                      <i
                        class="el-icon-loading"
                        v-if="item.batchRequestStatus===1"
                      ></i>
                      <div v-if="item.batchRequestStatus > 1 && item.batchResultCode!==0">
                        <i class="el-icon-error"></i>
                        <span :title="item.msg">{{item.msg || '失败'}}</span>
                      </div>
                    </td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
          <p class="tip-write">共{{totalNum}}条，成功{{successNum}}条，失败{{failedNum}}条，{{totalNum-successNum-failedNum}}条未处理</p>
        </div>
        <div class="content-bottom">
          <el-button
            type="success"
            class="batch-btn"
            @click="startNew"
            v-show="status===0"
          >开始</el-button>
          <el-button
            type="success"
            class="batch-btn"
            @click="continueBatch"
            v-show="status===2"
          >继续</el-button>
          <el-button
            type="danger"
            style="width: 95px"
            @click=suspendBatch
            v-show="status===1"
          >暂停</el-button>
          <el-button
            type="success"
            class="batch-btn"
            @click="handleClose"
            v-show="status===3"
          >已结束</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { get, post ,postJson } from "@/config/axios.config";
import { cloneDeep } from "lodash";
export default {
  name: "sz-batch-process",
  props: {
    title: {
      type: [Number, String],
      default: "批量处理"
    },
    width: {
      type: [Number, String],
      default: 700
    },
    height: {
      type: Number,
      default: 500
    },
    dialogVisible: {
      type: Boolean,
      default: false
    },
    tableData: {
      type: Array,
      default: () => {
        return [];
      },
      required: true
    },
    columns: {
      type: Array,
      default: () => {
        return [];
      },
      required: true
    },
    batchUrl: {
      type: String,
      default: "",
      required: true
    },
    dataValidate: {
      type: Function,
    },
    isJson: {
      type: Boolean,
      default: false
    },
    isSucessMsg: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    progress() {
      return (
        parseInt(((this.successNum + this.failedNum) * 100) / this.totalNum) ||
        0
      );
    }
  },
  data() {
    return {
      totalNum: 0,
      successNum: 0,
      failedNum: 0,
      status: 0,
      count: 0,
      impData: []
    };
  },
  methods: {
    handleClose() {
      if (this.status !== 0) {
        this.$emit("model", "");
        this.handleClose2();
      } else if (this.status === 0) {
        this.$confirm("此操作将停止执行, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          closeOnClickModal: false,
          type: "warning"
        })
          .then(() => {
            this.$emit("model", "noSearch");
            this.handleClose2();
          })
          .catch(() => {});
      }
    },
    startNew() {
      this.status = 1;
      if (this.impData && this.impData.length > 0) {
        let param = cloneDeep(this.impData[this.count]);
        this.oneRow(param);
      }
    },
    oneRow(param) {
      this.impData[this.count]["batchRequestStatus"] = 1;
      if(this.dataValidate){
        this.dataValidate(param).then(row => {
          this.sendRequest(row)
        }).catch(msg => {
          this.importError(msg)
        })
      }else{
        this.sendRequest(param)
      }
    },
    sendRequest(param){
      if(this.isJson){
        postJson(this.batchUrl, param, false, false)
        .then(res => {
          this.$set(this.impData[this.count],'batchRequestStatus',2);
          this.$set(this.impData[this.count],'batchResultCode',res.code);
          if (res.code === 0) {
            this.successNum++;
            if(this.isSucessMsg){
              this.impData[this.count]["msg"] = param.msg;
            }
          } else {
            this.failedNum++;
            this.impData[this.count]["msg"] = res.message;
          }
          if (this.impData.length > this.count + 1) {
            if (this.status === 1) {
              this.callBack();
            }
          } else {
            this.status = 3;
          }
        })
        .catch(r => {
          this.importError(r.message)
        });
      }else{
        post(this.batchUrl, param, false, false)
        .then(res => {
          this.$set(this.impData[this.count],'batchRequestStatus',2);
          this.$set(this.impData[this.count],'batchResultCode',res.code);
          if (res.code === 0) {
            this.successNum++;
            if(this.isSucessMsg){
              this.impData[this.count]["msg"] = param.msg;
            }
          } else {
            this.failedNum++;
            this.impData[this.count]["msg"] = res.message;
          }
          if (this.impData.length > this.count + 1) {
            if (this.status === 1) {
              this.callBack();
            }
          } else {
            this.status = 3;
          }
        })
        .catch(r => {
          this.importError(r.message)
        });
      }
    },
    importError(error){
      try {
        this.$set(this.impData[this.count],'msg',error);
      }
      finally {
        this.$set(this.impData[this.count],'batchRequestStatus',2);
        this.failedNum++;
        if(this.impData.length > this.count + 1){
          if(this.status === 1){
            this.callBack()
          }
        }else{
          this.status = 3;
        }
      }
    },
    callBack() {
      this.count++;
      let queryParam = cloneDeep(this.impData[this.count]);
      this.oneRow(queryParam);
    },
    suspendBatch() {
      this.status = 2;
    },
    continueBatch() {
      this.status = 1;
      this.callBack();
    },
    handleClose2() {
      this.successNum = 0;
      this.failedNum = 0;
      this.count = 0;
      this.status = 0;
    }
  },
  watch: {
    tableData(val) {
      this.impData = cloneDeep(val);
      this.totalNum = val.length;
    }
  }
};
</script>

<style lang="scss" scoped>
  .batch-content-box{
    background: #fff;
    .content-center{
      padding: 10px 10px 0;
      font-size: 13px;
      .line{
        margin: 2px 0 12px;
      }
      .width-full{
        width: 100%;
        overflow: auto;
        .scroll-table-box{
          display: inline-block;
          .table-head{
            td{
              height: 16px;
              text-align: center;
              padding: 10px 0;
              color: #333333;
              background: #eaf1f9;
            }
          }
          .table-body-box{
            overflow: auto;
            .table-body{
              td{
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                text-align: center;
                color: #666666;
                height: 30px;
                padding: 0;
                .el-icon-success{
                  color: #4acd9b;
                }
                .el-icon-error{
                  color: #fc4270;
                }
                .el-icon-loading{
                  color: #2191E5;
                  font-size: 18px;
                }
              }
              .error-msg{
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                width: 240px;
              }
              tr:nth-of-type(2n){
                background: #f0f7fd;
              }
              tr:hover{
                background: #fff4f0;
              }
            }
          }
        }
      }
      table{
        width: 100%;
        border-collapse: collapse;
        table-layout: fixed;
      }
      .tip-write{
        color: #e35664;
        font-size: 12px;
        text-align: right;
        margin-top: 16px;
      }
    }
    .content-bottom{
      height: 50px;
      line-height: 50px;
      text-align: center;
    }
  }
</style>
<style>
.content-bottom .el-button--success {
  background: #4acd9b;
  border-color: #4acd9b;
  min-width: 95px;
}
.content-bottom .el-button--success:hover,
.content-bottom .el-button--success:focus {
  background: #4acd9b;
  border-color: #4acd9b;
}
.batch-box .el-dialog__wrapper .el-dialog .el-dialog__body{
  padding: 0!important;
}
.table-body-box .table-body td > div{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
</style>
